// ============
// import
// ============
import * as bm from "../barrelModule";
/* app */
import { navigateAdapter } from "./adapter";

/**
 * type
 */

// ============
// slice
// ============
const navigateSlice = bm.createSlice({
  name: "navigate",
  initialState: navigateAdapter.getInitialState(),
  reducers: {
    upsertOneNavigateActive(state, action) {
      navigateAdapter.upsertOne(state, action.payload);
    },
  },
});

// ============
// reducer
// ============
export default navigateSlice.reducer;

// ============
// actions
// ============
export const { upsertOneNavigateActive } = navigateSlice.actions;
