import Box from "@mui/material/Box";
import { type Theme } from "@mui/material/styles";
/* app */
import { useMainContext } from "../../context/MainContextProvider";
import { themeSettings } from "../../theming/themeSettings";
import { RHeader } from "./page_header/RHeader";
import { TEWhatVal } from "../../enums/EWhat";
import { ELangs } from "../../enums/ELangs";
import type { TColors } from "../../types/main-types";
import { RMainChartWrapper } from "./charts/RMainChartWrapper";
import { webKitScrollbarFn } from "../../styled/WebKitScrollbar";

// =============
// types
// =============
export type TPageUtils = {
  colors: TColors;
  lang: ELangs;
};
type TComponentProps = {
  what: TEWhatVal;
};

// =============
// main
// =============
export function RMainContent({ what }: TComponentProps) {
  const { theme, lang } = useMainContext();
  const colors = themeSettings(theme.palette.mode);

  const pageUtils = {
    colors,
    lang,
  };

  // -------------
  // rendering
  // -------------
  return (
    <Box sx={_boxStyles(theme)} className="RMainContent">
      <RHeader title={what.toLowerCase()} utils={pageUtils} lang={lang} />
      <RMainChartWrapper target={what} lang={lang} />
    </Box>
  );
}

// =============
// styles
// =============
function _boxStyles(theme: Theme) {
  return {
    display: "grid",
    gridTemplateRows: "min-content 1fr",
    overflowY: "auto",
    ...webKitScrollbarFn(theme),
  };
}
