// =============
// input
// =============
import Stack from "@mui/material/Stack";
import InputBase from "@mui/material/InputBase";
import IconButton from "@mui/material/IconButton";
/* app */
import { iconsStore } from "../../hooks_g/useIcons";
import reference from "../../reference.json";
import { ELangs } from "../../enums/ELangs";
import type { TColors } from "../../types/main-types";

// =============
// types
// =============
type TComponentProps = {
  colors: TColors;
  lang: ELangs;
  isMobile: boolean;
};

// =============
// main
// =============
export function RTopbarSearch({ colors, lang, isMobile }: TComponentProps) {
  const SearchIcon = iconsStore.search;

  if (!isMobile) {
    return (
      <Stack direction="row" sx={_barStyles(colors)}>
        <InputBase
          placeholder={`${reference.utils.search.name[lang]}`}
          sx={_inputStyles(colors)}
        />
        <IconButton
          type="button"
          sx={{
            p: 1,
            color: colors.palette.neutral.light,
          }}
        >
          <SearchIcon />
        </IconButton>
      </Stack>
    );
  } else {
    return (
      <IconButton
        type="button"
        sx={{
          p: 1,
          color: "inherit",
        }}
      >
        <SearchIcon />
      </IconButton>
    );
  }
}

// =============
// styles
// =============
function _barStyles(colors: TColors) {
  return {
    backgroundColor: colors.palette.background.opposit,
    color: colors.palette.neutral.light,
    borderRadius: "3px",
  };
}

function _inputStyles(colors: TColors) {
  const isDark = colors.palette.mode === "dark";

  return {
    ml: 2,
    flex: 1,
    maxWidth: "30vw",
    color: isDark ? colors.palette.white.light : colors.palette.white.light,
    [`& .MuiInputBase-input::placeholder`]: {
      opacity: 0.8,
    },
  };
}
