// ============
// import
// ============
import React from "react";
import * as bm from "./barrelModule";
import { useMediaQuery, type Theme } from "@mui/material";
import { useMainContext } from "../../barrelModule";
// import { ETabsNames } from "../../tabs/RBasicTabs";

// ============
// types
// ============
type TComponentProps = {
  lang: bm.ELangs;
  colors: any;
  isDark: boolean;
  marginRight: number;
  // fnGetCurrentTabName: () => ETabsNames;
};
type TCountry = {
  countryId: bm.TECountriesVal;
  name: string;
  isActive: boolean;
  color: string;
};
type TStyledStack = {
  children: React.ReactNode | Array<React.ReactNode>;
  sx?: bm.SxProps<bm.Theme>;
  className?: string;
} & Record<string, any>;

// ============
// styled
// ============
const StyledStack = bm.styled((props: TStyledStack) => (
  <bm.Stack {...props}>{props.children}</bm.Stack>
))((allProps) =>
  Object.assign(
    {},
    {
      color: "inherit",
      "& .IIIg-noactive": {
        opacity: 0.7,
      },
    },
    {
      ...allProps.sx,
    }
  )
);

// ============
// main
// ============
export function RLineLegend({
  lang,
  // colors,
  // isDark,
  marginRight,
}: // fnGetCurrentTabName,
TComponentProps) {
  // const [toShowLegend, setToShowLegend] = React.useState(true);
  // const [currentTabName, setCurrentTabName] = React.useState<ETabsNames>(
  // ETabsNames.CHART
  // );

  const dispatch = bm.useAppDispatch();
  const cntrs = bm.useAppSelector(bm.selectAllCountries);

  const countries = bm.prepareCountries(lang, cntrs);
  const { CircleBound, CircleFill } = bm.iconsStore;

  const { theme } = useMainContext();
  const isMobile = !useMediaQuery(theme.breakpoints.up("sm"));

  // ------------
  // handlers
  // ------------
  const onActiveCountry =
    (country: TCountry) => (evnt: React.MouseEvent<HTMLButtonElement>) => {
      evnt.stopPropagation();

      window.setTimeout(() => {
        dispatch(
          bm.toggleCountryActive({
            id: country.countryId,
            changes: {
              active: !country.isActive,
            },
          })
        );
      }, 0);
    };

  // const newTabName = fnGetCurrentTabName();
  // const isCurrentTabNameChanged = newTabName !== currentTabName;
  // ------------
  // effects
  // ------------
  // React.useEffect(() => {
  //   // const newTabName = fnGetCurrentTabName();
  //   // if (newTabName !== currentTabName) {
  //   if (isCurrentTabNameChanged) {
  //     setCurrentTabName(newTabName);
  //     switch (newTabName) {
  //       case ETabsNames.DESCRIPTION:
  //         setToShowLegend(false);
  //         break;

  //       case ETabsNames.SOURCE:
  //         setToShowLegend(false);
  //         break;

  //       default:
  //         setToShowLegend(true);
  //         break;
  //     }
  //   }
  //   return () => {
  //     // setToShowLegend(true);
  //   };
  // }, [isCurrentTabNameChanged]);

  // ------------
  // rendering
  // ------------
  return (
    <StyledStack
      className="RLineLegend"
      direction="row"
      sx={_rlineLegendStyle(marginRight, isMobile /*, toShowLegend*/)}
    >
      <bm.RAllCntrsLegend
        countries={countries}
        lang={lang}
        isMobile={isMobile}
        theme={theme}
      />
      {countries.map((country) => (
        <bm.ButtonBase
          key={country.countryId}
          className="RLineLegend_ButtonBase"
          onClick={onActiveCountry(country)}
          sx={_buttonBaseStyle(isMobile, theme)}
        >
          <span style={_buttonIconStyle()}>
            {country.isActive ? (
              <CircleFill sx={{ color: country.color }} />
            ) : (
              <CircleBound />
            )}
          </span>
          <span
            style={_countryStyle()}
            className={country.isActive ? "" : "IIIg-NoActive"}
          >
            {country.name}
          </span>
        </bm.ButtonBase>
      ))}
    </StyledStack>
  );
}

// ============
// styles
// ============
function _rlineLegendStyle(
  marginRight: number,
  isMobile: boolean
  // toShowLegend: boolean
) {
  return {
    // opacity: toShowLegend ? 1 : 0,
    // visibility: toShowLegend ? "visible" : "hidden",
    width: `calc(100% - ${marginRight}px)`,
    marginTop: isMobile ? "1em" : "0",
    display: "grid",
    gridTemplateColumns: isMobile
      ? "repeat(2, 1fr)"
      : "repeat(auto-fill, 100px)",
    columnGap: "1rem",
    rowGap: isMobile ? "1em" : "0",
    [`& .MuiSvgIcon-root`]: {
      width: "0.9rem",
      height: "0.9rem",
    },
    [`& *`]: {
      transitionDuration: "500ms",
    },
  };
}

function _buttonBaseStyle(isMobile: boolean, theme: Theme) {
  return {
    // height: isMobile ? "" : "2em",
    justifyContent: "flex-start",
    fontSize: isMobile ? "16px" : "inherit",
    border: isMobile ? `1px solid ${theme.palette.secondary.main}` : "none",
    borderRadius: isMobile ? "4px" : "none",
    padding: isMobile ? "3px 9px" : "none",
  };
}

function _buttonIconStyle() {
  return {
    paddingLeft: "0.01rem",
    paddingRight: "0.01rem",
    paddingTop: "0.5rem",
    paddingBottom: "0.5rem",
    marginRight: "0.3rem",
  };
}

function _countryStyle() {
  return {
    marginBottom: "0.15rem",
    color: "inherit",
  };
}
