/**
 * A block of palette colors
 */

// ==========
// imports
// ==========
// import * as React from "react";
import { styled } from "@mui/material/styles";
import Typography from "@mui/material/Typography";

// ==========
// const
// ==========
// const COLUMN_DIMENSION = "10em";
const COLUMN_WIDTH = "10em";
const COLUMN_HEIGHT = "10em";

// ==========
// types
// ==========
type TComponentProps = {
  data: { title: string; colors: Record<string, any> };
};
type StyledPaletteColorProps = {
  bgColor: string;
};

// ==========
// styled
// ==========
const StyledWrapper = styled("div", {
  name: "SyledViewsBlock",
  slot: "Root",
})(() => ({
  display: "grid",
  gridTemplateColumns: `repeat(auto-fill, ${COLUMN_WIDTH})`,
}));

const StyledBlockHeader = styled(Typography, {
  name: "StyledViewsBlock",
  slot: "BlockHeader",
})(({ theme }) => ({
  color: theme.palette.error.main,
}));

const StyledElem = styled("div", {
  name: "StyledViewsBlock",
  slot: "Element",
})(() => ({
  display: "grid",
  gridTemplateRows: `${COLUMN_HEIGHT} fit-content(0px)`,
  justifyItems: "start",
  rowGap: "0.5em",
}));

const StyledPaletteColor = styled("div", {
  name: "StyledViewsBlock",
  slot: "PaletteColor",
  shouldForwardProp: (prop) => prop !== "bgColor",
})<StyledPaletteColorProps>(({ bgColor }) => ({
  width: "100%",
  height: "100%",
  backgroundColor: bgColor,
}));

const StyledPaletteColorName = styled(Typography, {
  name: "StyledViewsBlock",
  slot: "PaletteColorName",
})(({ theme }) => ({
  color: theme.palette.success.main,
}));

// ==========
// main
// ==========
export const StyledViewsBlock = ({ data }: TComponentProps) => {
  return (
    <div className="newBlock">
      <StyledBlockHeader variant="h5">{data.title}</StyledBlockHeader>
      <StyledWrapper>
        {data.colors.map(
          (entry: { key: string; value: string }, index: number) => (
            <StyledElem key={`${data.title}-${index}`}>
              <StyledPaletteColor bgColor={entry.value} />
              <StyledPaletteColorName variant="body2">
                {entry.key}
              </StyledPaletteColorName>
            </StyledElem>
          )
        )}
      </StyledWrapper>
    </div>
  );
};
