import * as bm from "../barrelModule";
import {
  gdp_Slice,
  gdp_per_capita_Slice,
  cpi_Slice,
  population_Slice,
} from "../main/store";

export function getGenericActions(target: bm.TEWhatVal) {
  switch (target) {
    // case -----------
    case "output_gdp":
      return gdp_Slice.actions;

    // case -----------
    case "output_gdp_per_capita":
      return gdp_per_capita_Slice.actions;

    // case -----------
    case "inflation_cpi":
      return cpi_Slice.actions;

    // case -----------
    case "population_population":
      return population_Slice.actions;

    // default -----------
    default:
      throw new Error("getGenericActions::[ERROR]: 'target' not found!");
  }
}
