// ============
// import
// ============
import { configureStore } from "@reduxjs/toolkit";
/* app */
// import gdpReducer from "../gdp/gdpSlice";
import countriesReducer from "../countries/countriesSlice";
// import populationReducer from "../population/populationSlice";
import genericReducer from "../generic/genericSlice";
import navigateReducer from "../navigate/navigateSlice";

// ============
// re-export
// ============
export const gdp_Slice = genericReducer("output_gdp");
export const gdp_per_capita_Slice = genericReducer("output_gdp_per_capita");
export const cpi_Slice = genericReducer("inflation_cpi");
export const population_Slice = genericReducer("population_population");

// ============
// main
// ============
export const store = configureStore({
  reducer: {
    countries: countriesReducer,
    // output_gdp: gdpReducer,
    output_gdp: gdp_Slice.reducer,
    output_gdp_per_capita: gdp_per_capita_Slice.reducer,
    // cpi: cpiReucer,
    inflation_cpi: cpi_Slice.reducer,
    // population: populationReducer,
    population_population: population_Slice.reducer,
    navigate: navigateReducer,
  },
});

// ============
// export types
// ============
export type RootState = ReturnType<typeof store.getState>;
export type AppDispatch = typeof store.dispatch;
