// ==========
// import
// ==========
import * as bm from "./barrelModule";
import Tab from "@mui/material/Tab";
import Tabs from "@mui/material/Tabs";
import { useNavigate } from "react-router-dom";
import { useMemo, useRef } from "react";
import { useAppDispatch } from "../charts/barrelModule";
// import { useAppSelector } from "../../../redux/hooks/storeHooks";
import { upsertOneNavigateActive } from "../../../redux/navigate/navigateSlice";
import { styled, type SxProps, type Theme } from "@mui/material/styles";
import { useLocation } from "react-router-dom";

// ==========
// types
// ==========
type TComponentProps = {
  target: bm.TEWhatVal;
  themeColors?: bm.TThemeColors;
  isTargetSidebarOpen?: boolean;
  saveStateOfTargetSidebarInContext?: (isOpen: boolean) => void;
};

type TTab = {
  label: string;
  onClick: (event: bm.React.MouseEvent<HTMLDivElement, MouseEvent>) => void;
  sx?: SxProps<Theme>;
  className: string;
};

type TTabs = {
  children: bm.React.ReactNode | Array<bm.React.ReactNode>;
  sx?: SxProps<Theme>;
  orientation?: "vertical" | "horizontal";
  value: number;
  onChange: (_event: bm.React.SyntheticEvent, newValue: number) => void;
  "aria-label": string;
} & Record<string, any>;

type TNavTabs = {
  className?: string;
  sx?: SxProps<Theme>;
  children?: bm.React.ReactNode | Array<bm.React.ReactNode>;
};

// ==========
// styled
// ==========
const StyledTabs = styled(
  (props: TTabs) => <Tabs {...props}>{props.children}</Tabs>,
  {
    shouldForwardProp: (prop) =>
      [
        "children",
        "sx",
        "orientation",
        "value",
        "onChange",
        "aria-label",
      ].includes(prop as string),
  }
)((allProps) =>
  Object.assign(
    {},
    {
      color: "inherit",
      [`& .MuiTabs-indicator`]: {
        left: 0,
        width: "4px",
        backgroundColor:
          (
            allProps.theme.palette
              .secondary as typeof allProps.theme.palette.secondary &
              Record<"other", string>
          ).other["400"] + "A1",
      },
      overflow: "clip",
      "& .MuiTab-root.Mui-selected": {
        color: "inherit",
        opacity: 0.7,
      },
    },
    { ...allProps.sx }
  )
);

const StyledTab = styled((props: TTab) => <Tab {...props} />)((allProps) =>
  Object.assign(
    {},
    {
      color: "inherit",
      alignItems: "start",

      /* some bag with Types for 'whiteSpace' */
      "&": {
        whiteSpace: "nowrap",
      },
    },
    { ...allProps.sx }
  )
);

const StyledNavTabs = styled((props: TNavTabs) => (
  <bm.Box {...props}>{props.children}</bm.Box>
))((allProps) =>
  Object.assign(
    {},
    {
      width: "100%",
      // "4F" - alpha
      borderLeft: `1px solid ${
        (
          allProps.theme.palette
            .secondary as typeof allProps.theme.palette.secondary &
            Record<"other", string>
        ).other["400"] + "A3"
      }`,
    },
    { ...allProps.sx }
  )
);

// ==========
// main
// ==========
export function RTargetsNavTabs({
  target,
  // themeColors,
  isTargetSidebarOpen = undefined,
  saveStateOfTargetSidebarInContext = undefined,
}: TComponentProps) {
  const routes = bm.useListItemsStack(target);
  const navigate = useNavigate();
  const dispatch = useAppDispatch();
  const routerPathname = useLocation().pathname;

  // ------------
  // local store
  // ------------
  const flag = useRef(false);
  const value = useRef(0);
  const localStore = useRef<Record<string, Record<string, number | string>>>(
    {}
  );
  const ls = localStore.current;

  // ------------
  //
  // ------------
  const groupTarget =
    target.indexOf("_") === -1 ? target : target.slice(0, target.indexOf("_"));

  if (ls[groupTarget] === undefined) {
    ls[groupTarget] = {} as Record<string, number | string>;
  }

  if (ls[groupTarget][target] === undefined) {
    ls[groupTarget][target] = `/${target}`;
  }

  if (ls[groupTarget].value === undefined) {
    const initValueArray = Object.entries(routes).find(
      ([_key, valueObj]) => valueObj.to === routerPathname
    );

    if (initValueArray !== undefined) {
      value.current = Number(initValueArray[0]);
    } else {
      value.current = 0;
    }
  } else {
    value.current = ls[groupTarget].value as number;
  }

  // ----------
  // memo
  // ----------
  const TabsMemoComponent = useMemo(() => {
    // ------------
    // handlers
    // ------------
    const handleClick = (event: any, route: string) => {
      event.preventDefault();

      const DELAY = 0;
      const parent =
        route.indexOf("_") === -1
          ? route.slice(1)
          : route.slice(1, route.indexOf("_"));

      window.setTimeout(() => {
        navigate(route);
        dispatch(
          upsertOneNavigateActive({
            id: parent,
            active: route,
          })
        );

        if (
          isTargetSidebarOpen !== undefined &&
          saveStateOfTargetSidebarInContext !== undefined
        ) {
          saveStateOfTargetSidebarInContext(!isTargetSidebarOpen);
        }
      }, DELAY);
    };

    const handleChange = (_event: React.SyntheticEvent, newValue: number) => {
      ls[groupTarget].value = newValue;
      flag.current = true;
    };

    // --------
    // rendering
    // --------
    return (
      <StyledTabs
        orientation="vertical"
        value={value.current}
        onChange={handleChange}
        aria-label="nav tabs"
      >
        {routes.map((route) => {
          return (
            <StyledTab
              className="RTargetsNavTabs_StyledTabs_StyledTab"
              key={route.id}
              label={route.title}
              onClick={(event) => handleClick(event, route.to)}
            />
          );
        })}
      </StyledTabs>
    );
  }, [
    value,
    routes,
    dispatch,
    ls,
    groupTarget,
    navigate,
    isTargetSidebarOpen,
    saveStateOfTargetSidebarInContext,
  ]);

  // ----------
  // rendering
  // ----------
  return (
    <StyledNavTabs className="RTargetsNavTabs">
      {TabsMemoComponent}
    </StyledNavTabs>
  );
}
