// =========
// import
// =========
import * as React from "react";
import Box from "@mui/material/Box";
import Slider from "@mui/material/Slider";
import { styled, type Theme } from "@mui/material/styles";

// =========
// types
// =========
type TComponentProps = {
  xArray: Array<string> | null;
  handleChangedXRange: (range: [number, number]) => void;
  rangeTupple: [] | [number, number];
  className?: string;
  sx?: Record<any, any>;
};
type TSliderProps = {
  value: [number, number];
  onChange: (event: Event, newValue: number | Array<number>) => void;
  valueLabelDisplay: "auto" | "on" | "off" | undefined;
  step: number;
  min: number;
  max: number;
};

// =========
// styled
// =========
const StyledSlider = styled((props: TSliderProps) => <Slider {...props} />)(
  ({ theme }: { theme: Theme }) => ({
    color:
      theme.palette.mode === "dark"
        ? theme.palette.primary.main
        : (
            theme.palette.primary as typeof theme.palette.primary & {
              other: Record<string, any>;
            }
          ).other["200"],
  })
);

// =========
// main
// =========
export function RRangeSlider(props: TComponentProps) {
  const {
    xArray,
    handleChangedXRange,
    rangeTupple,
    className,
    sx: outerSX,
  } = props;

  const NAME = "RRangeSlider";

  // ---------
  // useState
  // ---------
  const [rangeValues, setRangeValues] = React.useState<[number, number]>(
    [] as unknown as [number, number]
  );
  const [isJustMounted, setIsJustMounted] = React.useState(true);

  // ---------
  // useRef
  // ---------
  const localStore = React.useRef({
    range: [] as unknown as [number, number],
    isTransferDataBlocked: false as boolean,
    greenRangeValues: rangeValues,
    isDataChangingStoped: true,
  });

  // ---------
  // utils
  // ---------
  const transferRangeData = (newValue: [number, number]) => {
    const DELAY = 300;

    const timeoutID = window.setTimeout(() => {
      if (
        localStore.current.greenRangeValues[0] === newValue[0] &&
        localStore.current.greenRangeValues[1] === newValue[1]
      ) {
        localStore.current.isTransferDataBlocked = false;
        handleChangedXRange(
          localStore.current.greenRangeValues as [number, number]
        );
      } else {
        clearTimeout(timeoutID);
        transferRangeData(
          localStore.current.greenRangeValues as [number, number]
        );
      }
    }, DELAY);
  };

  // ---------
  // handlers
  // ---------
  const handleChange = (
    event: Event,
    newValue: number | Array<number>
  ): void => {
    event.preventDefault();
    setRangeValues(newValue as [number, number]);

    localStore.current.greenRangeValues = newValue as [number, number];

    if (!localStore.current.isTransferDataBlocked) {
      localStore.current.isTransferDataBlocked = true;
      transferRangeData(newValue as [number, number]);
    }
  };

  // ---------
  // useEffect
  // ---------
  React.useEffect(() => {
    if (isJustMounted && xArray !== null) {
      const xArrayNumber = xArray.map((elem) => Number(elem));
      localStore.current.range = [
        xArrayNumber[0],
        xArrayNumber[xArrayNumber.length - 1],
      ];
      setRangeValues(localStore.current.range);
      setIsJustMounted(false);

      if (rangeTupple.length > 0) {
        setRangeValues(rangeTupple as [number, number]);
      }
    }

    // return () => console.info("%cRRangeSlider::UNMOUNTED", "color: red");
  }, [xArray, isJustMounted, rangeTupple]);

  // ---------
  // rendering
  // ---------
  return (
    <Box
      sx={Object.assign({}, { width: "100%" }, outerSX)}
      className={className + "_" + NAME}
    >
      <StyledSlider
        value={rangeValues}
        onChange={handleChange}
        valueLabelDisplay="auto"
        step={1}
        min={localStore.current.range[0]}
        max={localStore.current.range[1]}
      />
    </Box>
  );
}
