// import * as React from "react";
import React from "react";
import { iconsStore } from "../../hooks_g/useIcons";
import { IconButton } from "@mui/material";
import { useMainContext } from "../../context/MainContextProvider";

export function RTopbarTargetSidebar() {
  // ----------
  // local state
  // ----------

  // ----------
  // from context
  // ----------
  const { isTargetSidebarOpen, saveStateOfTargetSidebarInContext } =
    useMainContext();

  // ----------
  // icons
  // ----------
  const { ListIcon } = iconsStore;

  // ----------
  // handlers
  // ----------
  const handlerButtonClick = (evnt: React.MouseEvent) => {
    evnt.stopPropagation();
    saveStateOfTargetSidebarInContext(!isTargetSidebarOpen);
  };

  // console.log(
  //   "%cRTopbarTargetSidebar::isSidebarOpen: ",
  //   "color: yellow",
  //   isSidebarOpen
  // );

  // ----------
  // rendering
  // ----------
  return (
    <IconButton
      className="RTopbarTargetSidebar"
      type="button"
      sx={{
        p: 1,
        color: "inherit",
      }}
      onClick={handlerButtonClick}
    >
      <ListIcon />
    </IconButton>
  );
}
