// =======
// import
// =======
import * as bm from "./barrelModule";
import { type PaletteColor, type Theme } from "@mui/material/styles";

// =======
// main
// =======
export function RAllCntrsLegend({
  countries,
  lang,
  isMobile,
  theme,
}: {
  countries: any;
  lang: bm.ELangs;
  isMobile: boolean;
  theme: Theme;
}) {
  const { NotInterested, Checked } = bm.iconsStore;
  const dispatch = bm.useAppDispatch();
  const isSomeActive = countries.some(
    (cntr: { isActive: boolean }) => cntr.isActive === true
  );
  const isAllActive = !countries.some(
    (cntr: { isActive: boolean }) => cntr.isActive === false
  );

  // ------------
  // handlers
  // ------------
  const onAllCountriesActive =
    (cntrs: Array<any>, active: boolean) =>
    (evnt: React.MouseEvent<HTMLButtonElement>) => {
      evnt.stopPropagation();
      const resultArray = cntrs.map((cntr: any) => {
        return {
          id: cntr.countryId,
          changes: {
            active: active,
          },
        };
      });

      window.setTimeout(() => dispatch(bm.toggleAllCountries(resultArray)), 0);
    };

  // ------------
  // rendering
  // ------------
  return (
    <>
      <bm.ButtonBase
        className="RLineLegend_AllCountries_ON_OFF"
        sx={_buttonWCStyle(isMobile, theme)}
        onClick={onAllCountriesActive(countries, true)}
      >
        <span style={_buttonIconStyle()}>
          <Checked
            sx={{
              color: isAllActive
                ? (theme) => {
                    return (
                      theme.palette.primary as PaletteColor & {
                        other: string;
                      }
                    ).other["400"];
                  }
                : (theme) => theme.palette.success.main,
            }}
          />
        </span>
        <bm.Box
          component="span"
          className="RLineLegend_AllCountries_Switch"
          sx={() => _styleButtonCapture(isAllActive, "on")}
        >
          {bm.reference.utils.allCountries.on.name[lang]}
        </bm.Box>
      </bm.ButtonBase>
      <bm.ButtonBase
        className="RLineLegend_AllCountries_ON_OFF"
        sx={_buttonWCStyle(isMobile, theme)}
        onClick={onAllCountriesActive(countries, false)}
      >
        <span style={_buttonIconStyle()}>
          <NotInterested
            sx={{
              color: isSomeActive
                ? (theme) => theme.palette.error.main
                : (theme) =>
                    (theme.palette.primary as PaletteColor & { other: string })
                      .other["400"],
            }}
          />
        </span>
        <bm.Box
          component="span"
          className="RLineLegend_AllCountries_Switch"
          sx={() => _styleButtonCapture(isSomeActive, "off")}
        >
          {bm.reference.utils.allCountries.off.name[lang]}
        </bm.Box>
      </bm.ButtonBase>
    </>
  );
}

// ============
// styles
// ============
function _buttonWCStyle(isMobile: boolean, theme: Theme) {
  return {
    // height: "2em",
    gap: "0.3rem",
    justifyContent: "flex-start",
    fontSize: isMobile ? "16px" : "inherit",
    border: isMobile ? `1px solid ${theme.palette.secondary.main}` : "none",
    borderRadius: isMobile ? "4px" : "none",
    padding: isMobile ? "3px 9px" : "none",
    "& .MuiSvgIcon-root": {
      width: "0.8em",
      height: "0.8em",
    },
    "& .RLineLegend_AllCountries_Switch": {
      marginBottom: "0.2em",
    },
  };
}

function _styleButtonCapture(
  // theme: any,
  isActive: boolean,
  capture: "on" | "off"
) {
  if (capture === "off") {
    return {
      color: "inherit",
      opacity: isActive === true ? 1 : 0.7,
    };
  } else {
    return {
      color: "inherit",
      opacity: isActive === true ? 0.7 : 1,
    };
  }
}

function _buttonIconStyle() {
  return {
    // paddingLeft: "0.01rem",
    // paddingRight: "0.01rem",
    paddingTop: "0.5rem",
    paddingBottom: "0.5rem",
    // marginRight: "0.3rem",
  };
}
