// =========
// import
// =========
import * as bm from "./barrelModule";

// =========
// types
// =========
type TComponentProps = {
  children: bm.React.ReactElement | Array<bm.React.ReactElement>;
  name?: string;
  slot?: string;
  sx?: bm.SxProps<bm.Theme>;
  className?: string;
} & Record<string, any>;

// =========
// main
// =========
export const RStyledHorizontalTabsWrap = bm.styled(
  (props: TComponentProps) => <bm.Box {...props}>{props.children}</bm.Box>,
  {
    name: "RStyledHorizontalTabsWrap",
    slot: "Root",
  }
)((allProps) => {
  return Object.assign(
    {},
    {
      color: "inherit",
      width: "100%",
      marginTop: "1em",
    },
    {
      ...allProps.sx,
    }
  );
});
