// =============
// import
import type { OverridableComponent } from "@mui/material/OverridableComponent";
import type { SvgIconTypeMap } from "@mui/material";
/* */
import config from "../../../config.json";
import reference from "../../../reference.json";
import { useMainContext } from "../../../context/MainContextProvider";
import { iconsStore } from "../../../hooks_g/useIcons";

// =============
// types
// =============
type TRoutesKeys = keyof typeof config.layout.sidebar.routes;
type TRoutes = {
  id: string;
  name: string;
  to: string;
  icon: string;
};
type TRawRoutes = {
  [K in TRoutesKeys]: TRoutes;
};
type TSortedRoutes = TRoutes[];
export type TSidebarRoute = {
  id: string;
  title: string;
  to: string;
  icon: TIcon;
};
type TIcon = OverridableComponent<SvgIconTypeMap<{}, "svg">> & {
  muiName: string;
};

// =============
// main
// =============
export function useItemsStack() {
  const rawRoutes = config.layout.sidebar.routes;
  const sortedRoutes = _sortRoutes(rawRoutes);

  const [packagedRoutes, err] = _packageRoutes(sortedRoutes) as [
    Array<TSidebarRoute>,
    string | null
  ];

  if (err !== null) {
    throw new Error(`useItemsStack::[ERROR]: ${err}`);
  }

  return packagedRoutes;
}

// -------------
// _sortRoutes
// -------------
function _sortRoutes(rawRoutes: TRawRoutes) {
  const resultRoutesArray: TSortedRoutes = [];
  const sortTarget = config.layout.sidebar.sort as Array<TRoutesKeys>;

  sortTarget.forEach((id) => {
    resultRoutesArray.push(rawRoutes[id]);
  });

  return resultRoutesArray;
}

// -------------
// _packageRoutes
// -------------
function _packageRoutes(sortedRoutes: TSortedRoutes) {
  // type TTargetKeys =
  //   | keyof typeof reference.targets
  //   | keyof typeof reference.greetings;
  type TTargetKeys = keyof typeof reference.directory.sections;

  let err = null;

  const { lang } = useMainContext();
  // const targets = Object.assign({}, reference.greetings, reference.targets);
  const targets = Object.assign({}, reference.directory.sections);

  const packagedRoutesArray = [] as Array<TSidebarRoute>;

  for (const routeObj of sortedRoutes) {
    const packagedRoute = {
      id: "",
      title: "",
      to: "",
      icon: {} as TIcon,
    };

    const routeName = routeObj.name as TTargetKeys;

    if (!targets[routeName]) {
      err = `_packageRoutes: 'targets[routeName] = ${routeName}' is not found`;

      return [packagedRoutesArray, err];
    }

    packagedRoute.id = routeObj.id;
    packagedRoute.title = targets[routeName].name[lang];
    packagedRoute.to = routeObj.to;
    [packagedRoute.icon, err] = _insertIcon(routeObj.icon) as [
      TIcon,
      string | null
    ];

    if (err !== null) {
      return [packagedRoutesArray, err];
    }

    packagedRoutesArray.push(packagedRoute);
  }

  return [packagedRoutesArray, err];
}

// -------------
// _insertIcon
// -------------
function _insertIcon(iconName: string) {
  let err = null;

  if (!iconsStore[iconName]) {
    err = `_insertIcon: 'iconsStore[iconName] = ${iconName}' is not found`;

    return [{}, err];
  }

  return [iconsStore[iconName], err];
}
