// ===========
// import
// ===========
import { ECountries } from "../enums/ECountries";
import type { TECountriesVal } from "../enums/ECountries";
import { addManyCountries } from "../redux/countries/countriesSlice";
import { useAppDispatch } from "../redux/hooks/storeHooks";

// ===========
// main
// ===========
export function useInitApp() {
  const dispatch = useAppDispatch();

  dispatch(addManyCountries(_countries()));
}

// ===========
// utils
// ===========
function _countries(): Array<{ id: TECountriesVal; active: boolean }> {
  const cntrs = Object.values(ECountries) as Array<TECountriesVal>;

  return cntrs.map((cntr) => ({
    id: cntr,
    active: true,
  }));
}
