// ===========
// import
// ===========
import * as React from "react";
import { styled, Theme } from "@mui/material/styles";
// import styled from "../../../mui-material/src/styles/styled";
import { Typography } from "@mui/material";
import reference from "../../../reference.json";
import { ELangs } from "../../../enums/ELangs";
import { prepareCountries } from "../../../func/prepare/prepareCountries";
import { selectAll as selectAllCountries } from "../../../redux/countries/adapter";
import {
  useAppDispatch,
  useAppSelector,
} from "../../../redux/hooks/storeHooks";
import { ButtonBase } from "@mui/material";
import { iconsStore } from "../../../hooks_g/useIcons";
import {
  toggleCountryActive,
  toggleAllCountries,
} from "../../../redux/countries/countriesSlice";
import { ButtonGroup } from "@mui/material";
import { Box } from "@mui/material";
import { useMediaQuery } from "@mui/material";
import { useMainContext } from "../../../context/MainContextProvider";

// ===========
// styled components
// ===========
const GridStyledContent = styled("section")(() => {
  return {
    display: "grid",
    gridTemplateColumns: "repeat(auto-fit, minmax(260px, 1fr))",
    columnGap: "3rem",
  };
});

const GridStyledContainer = styled("div")((props) => {
  return {
    display: "grid",
    gridTemplateRows: "fit-content(2rem) 1fr",
    padding: "2rem 1rem 1rem 0",
    rowGap: "1rem",
    color: props.theme.palette.text.primary,
  };
});

const FlexStyledContainer = styled("div")(() => {
  return {
    display: "flex",
    flexDirection: "column",
    // marginBottom: "1rem",
  };
});

// ===========
// types
// ===========
type ComponentProps = {
  lang: ELangs;
};

// ===========
// subcomponents
// ===========
function ButtonWithCountries(props: {
  country: {
    countryId: string;
    name: string;
    isActive: boolean;
    color: string;
  };
  isMobile: boolean;
  theme: Theme;
}) {
  const { country } = props;
  const { CircleBound, CircleFill } = iconsStore;
  const dispatch = useAppDispatch();

  // ------------
  // handlers
  // ------------
  const onActiveCountry =
    (country: any) => (evnt: React.MouseEvent<HTMLButtonElement>) => {
      evnt.stopPropagation();

      window.setTimeout(
        () =>
          dispatch(
            toggleCountryActive({
              id: country.countryId,
              changes: {
                active: !country.isActive,
              },
            })
          ),
        0
      );
    };

  // ------------
  // rendering
  // ------------
  return (
    <ButtonBase
      key={country.countryId}
      className="RStartTab_ButtonWithCountries"
      sx={_buttonWCStyle(props.isMobile, props.theme)}
      onClick={onActiveCountry(country)}
    >
      <span>
        {country.isActive ? (
          <CircleFill sx={{ color: country.color }} />
        ) : (
          <CircleBound />
        )}
      </span>
      <span className="RStartTab_ButtonWithCountries_CountryName">
        {country.name}
      </span>
    </ButtonBase>
  );
}

function ButtonGroupAllCntrs({
  countries,
  lang,
  isMobile = false,
  theme,
}: {
  countries: any;
  lang: ELangs;
  isMobile?: boolean;
  theme?: Theme;
}) {
  const { NotInterested, Checked } = iconsStore;
  const dispatch = useAppDispatch();

  // ------------
  // handlers
  // ------------
  const onAllCountriesActive =
    (cntrs: Array<any>, active: boolean) =>
    (evnt: React.MouseEvent<HTMLButtonElement>) => {
      evnt.stopPropagation();
      const resultArray = cntrs.map((cntr: any) => {
        return {
          id: cntr.id,
          changes: {
            active: active,
          },
        };
      });

      window.setTimeout(() => dispatch(toggleAllCountries(resultArray)), 0);
    };

  // ------------
  // rendering
  // ------------
  return (
    <ButtonGroup
      variant="outlined"
      aria-label="outlined button group"
      orientation="vertical"
    >
      <ButtonBase
        className="RStartTab_ButtonWithCountries_AllCountries_ON"
        sx={_buttonWCStyle(isMobile, theme)}
        onClick={onAllCountriesActive(countries, true)}
      >
        <span>
          <Checked sx={{ color: (theme) => theme.palette.success.main }} />
        </span>
        <span className="RStartTab_ButtonWithCountries_CountryName">
          {reference.utils.allCountries.on.name[lang]}
        </span>
      </ButtonBase>
      <ButtonBase
        className="RStartTab_ButtonWithCountries_AllCountries_OFF"
        sx={_buttonWCStyle(isMobile, theme)}
        onClick={onAllCountriesActive(countries, false)}
      >
        <span>
          <NotInterested sx={{ color: (theme) => theme.palette.error.main }} />
        </span>
        <span className="RStartTab_ButtonWithCountries_CountryName">
          {reference.utils.allCountries.off.name[lang]}
        </span>
      </ButtonBase>
    </ButtonGroup>
  );
}

// ===========
// main
// ===========
export function RStartTab(props: ComponentProps) {
  const countryHeadersObj = reference.headers.startPage.countries;
  const cntrs = useAppSelector(selectAllCountries);
  const countries = prepareCountries(props.lang, cntrs);
  const allCountries = countries.length;
  const activeCountries = countries.filter(
    (country) => country.isActive === true
  ).length;
  const { theme } = useMainContext();
  const isWiderThanMobileScreen = useMediaQuery(theme.breakpoints.up("sm"));

  // ----------
  // rendering
  // ----------
  return (
    <GridStyledContainer className="RStartTab_GridStyledContainer">
      <Box
        className="RStartTab_GridStyledContainer_Header"
        sx={{ marginBottom: "1em" }}
      >
        <Typography variant="h3" component="h2" sx={{ marginBottom: "0.2em" }}>
          {reference.greetings.startPage.selection[props.lang]}
        </Typography>
        <Typography component="h4">
          {reference.greetings.startPage.selected.part1[props.lang] +
            ` ${activeCountries} ` +
            reference.greetings.startPage.selected.part2[props.lang] +
            ` ${allCountries}`}
        </Typography>
      </Box>
      <GridStyledContent>
        <FlexStyledContainer sx={{ marginBottom: "1rem", rowGap: "0.5rem" }}>
          <Typography
            variant="h5"
            sx={{
              borderBottom: (theme) =>
                `1px solid ${theme.palette.primary.main}`,
              textTransform: "uppercase",
              marginTop: "2em",
            }}
          >
            {reference.headers.startPage.allCountries.all.name[props.lang]}
          </Typography>
          <ButtonGroupAllCntrs
            countries={cntrs}
            lang={props.lang}
            isMobile={!isWiderThanMobileScreen}
            theme={theme}
          />
        </FlexStyledContainer>
        {Object.values(countryHeadersObj).map((countryObj) => (
          <FlexStyledContainer
            key={countryObj.id}
            sx={{ marginBottom: "1rem", rowGap: "0.5rem" }}
          >
            <Typography
              variant="h5"
              sx={{
                borderBottom: (theme) =>
                  `1px solid ${theme.palette.primary.main}`,
                // textTransform: isWiderThanMobileScreen ? "none" : "uppercase",
                textTransform: "uppercase",
                // marginTop: isWiderThanMobileScreen ? "none" : "2em",
                marginTop: "2em",
              }}
            >
              {countryObj.name[props.lang]}
            </Typography>
            <FlexStyledContainer>
              {countries
                .filter(
                  (countryRefObj) => countryRefObj.region === countryObj.id
                )
                .map((searchedCountry) => (
                  <ButtonWithCountries
                    key={searchedCountry.countryId}
                    country={searchedCountry}
                    isMobile={!isWiderThanMobileScreen}
                    theme={theme}
                  />
                ))}
            </FlexStyledContainer>
          </FlexStyledContainer>
        ))}
      </GridStyledContent>
    </GridStyledContainer>
  );
}

// ============
// styles
// ============
function _buttonWCStyle(isMobile: boolean = false, theme?: Theme) {
  return {
    justifyContent: "flex-start",
    fontSize: isMobile ? "16px" : "inherit",
    padding: isMobile ? "9px 27px" : "inherit",
    border: isMobile ? `1px solid ${theme?.palette.secondary.main}` : "inherit",
    borderRadius: isMobile ? "4px" : "inherit",
    width: isMobile ? "200px" : "inherit",
    margin: isMobile ? "8px" : "inherit",
    marginLeft: isMobile ? "0" : "inherit",
    "& .MuiSvgIcon-root": {
      width: "0.8em",
      height: "0.8em",
    },
    "& .RStartTab_ButtonWithCountries_CountryName": {
      marginBottom: "0.2em",
      marginLeft: "1em",
    },
  };
}
