// ============
// import
// ============
import * as bm from "../barrelModule";

// ============
// types
// ============
type TRoutesKeys = keyof typeof bm.reference.targets;
type TRoutes = {
  id: string;
  name: string;
  to: string;
  icon?: string;
};
type TTargetObj = {
  sort: Array<string>;
  routes: { [x: string]: TRoutes };
};
type TSortedRoutes = TRoutes[];
export type TTargetsBarRoute = {
  id: string;
  title: string;
  to: string;
  icon?: TIcon;
  name: string;
};
type TIcon = bm.OverridableComponent<bm.SvgIconTypeMap<{}, "svg">> & {
  muiName: string;
};

// ============
// main
// ============
export function useListItemsStack(
  target: TRoutesKeys
): Array<TTargetsBarRoute> {
  const targetsDirectory = bm.config.layout.targetsbar.targets;
  const inxOf_ = target.indexOf("_");
  const majorTarget =
    inxOf_ === -1
      ? (target as keyof typeof targetsDirectory)
      : (target.substring(0, inxOf_) as keyof typeof targetsDirectory);

  const rawRoutes = bm.config.layout.targetsbar.targets[majorTarget];
  const sortedRoutes = _sortRoutes(rawRoutes);
  const [packageRoutes, err] = _packageRoutes(sortedRoutes);

  if (err !== null) {
    throw new Error(`useListItemsStack::[ERROR]: ${err}`);
  }

  return packageRoutes;
}

// ------------
// _sortRoutes
// ------------
function _sortRoutes(rawRoutes: TTargetObj) {
  const resultRoutesArray: TSortedRoutes = [];
  const routes = rawRoutes.routes;
  const sortTarget = rawRoutes.sort;

  sortTarget.forEach((id) => {
    resultRoutesArray.push(routes[id]);
  });

  return resultRoutesArray;
}

// ------------
// _packageRoutes
// ------------
function _packageRoutes(
  sortedRoutes: TSortedRoutes
): [Array<TTargetsBarRoute>, string | null] {
  let err = null;
  const { lang } = bm.useMainContext();
  const targets = bm.reference.targets;
  let packageRoutesArray = [] as Array<TTargetsBarRoute>;

  const isError = sortedRoutes.find(
    (obj) => targets[obj.name as keyof typeof targets] === undefined
  );

  if (isError !== undefined) {
    err = `_packageRoutes: targets[${isError.name}] is not found`;
    return [packageRoutesArray, err];
  }

  packageRoutesArray = sortedRoutes.map((obj) => {
    return {
      ...obj,
      title: targets[obj.name as keyof typeof targets].shortName[lang],
      icon: undefined,
    };
  });

  return [packageRoutesArray, err];
}
