// =============
// input
// =============
import Stack from "@mui/material/Stack";
import IconButton from "@mui/material/IconButton";
/* app */
import { iconsStore } from "../../hooks_g/useIcons";
import { ELangs } from "../../enums/ELangs";
import { RTopbarSwitchLang } from "./RTopbarSwitchLang";

// =============
// types
// =============
type TComponentProps = {
  colors: { palette: { [x: string]: any } };
  toggleColorMode: () => void;
  changeLang: (lang: ELangs) => void;
  lang: ELangs;
  isMobile: boolean;
};

// =============
// main
// =============
export function RTopbarIcons({
  toggleColorMode,
  changeLang,
  lang,
  colors,
}: // isMobile = false,
TComponentProps) {
  const LightModeIcon = iconsStore.lightMode;
  const DarkModeIcon = iconsStore.darkMode;

  return (
    <Stack className="RTopbarIcons" direction="row" spacing={2}>
      <IconButton onClick={toggleColorMode} sx={{ color: "inherit" }}>
        {colors.palette.mode === "dark" ? <LightModeIcon /> : <DarkModeIcon />}
      </IconButton>
      <RTopbarSwitchLang colors={colors} lang={lang} changeLang={changeLang} />
    </Stack>
  );
}
