// ===========
// import
// ===========
import ListItem from "@mui/material/ListItem";
import ListItemButton from "@mui/material/ListItemButton";
import ListItemIcon from "@mui/material/ListItemIcon";
import ListItemText from "@mui/material/ListItemText";
import { Box } from "@mui/material";
import { useAppSelector } from "../../redux/hooks/storeHooks";
import { selectById as navigateSelectById } from "../../redux/navigate/adapter";
import type { TSidebarRoute } from "./hooks/useItemsStack";
import { useNavigate } from "react-router-dom";
import { styled } from "@mui/material/styles";
import React from "react";

// ===========
// types
// ===========
type TListItemIcon = {
  children: React.ReactNode;
  open: boolean;
  className: string;
};
type TListItemText = {
  primary: string;
  open: boolean;
  className: string;
  fnIsAnimationStarted: () => boolean;
};
type TListItemButton = {
  children: React.ReactNode;
  open: boolean;
  className: string;
  onClick: (event: React.MouseEvent<HTMLDivElement, MouseEvent>) => void;
};
type TDivider = {
  component: "div";
  className: string;
  isActive: boolean;
};
type TComponentProps = {
  route: TSidebarRoute;
  open: boolean;
  handleIsActive: (activeListItemId: string) => void;
  activeListItem: string;
  handleDrawerClose: () => void;
  isMobile: boolean;
  fnIsAnimationStarted: () => boolean;
};

// ===========
// styled
// ===========
const StyledListItemIcon = styled(
  (props: TListItemIcon) => (
    <ListItemIcon {...props}>{props.children}</ListItemIcon>
  ),
  {
    shouldForwardProp: (prop) => prop !== "open",
  }
)((props) => {
  const PL = "4.108px"; //   
  return {
    minWidth: 0,
    paddingLeft: PL,
    mr: props.open ? 3 : "auto",
    justifyContent: "center",
    color: "inherit",
    "& .MuiSvgIcon-root": {
      height: "1.5rem",
      width: "1.5rem",
    },
    opacity: props.theme.palette.mode === "dark" ? 0.7 : 1,
  };
});

const StyledListItemText = styled(
  (props: TListItemText) => <ListItemText {...props} />,
  {
    shouldForwardProp: (prop) =>
      prop !== "open" && prop !== "fnIsAnimationStarted",
  }
)((props) => {
  return {
    opacity: props.open ? 1 : props.fnIsAnimationStarted() ? 1 : 0,
    whiteSpace: "nowrap",
    color: "inherit",
    paddingTop: "0.2em",
    // transition: props.open ? "opacity 1ms 50ms" : "none",
  };
});

const StyledListItemButton = styled((props: TListItemButton) => (
  <ListItemButton {...props}>{props.children}</ListItemButton>
))(() => ({
  height: 48,
  justifyContent: "flex-start",
  px: 2.5,
  gap: "1em",
}));

const StyledDivider = styled((props: TDivider) => <Box {...props} />, {
  shouldForwardProp: (prop) => prop !== "isActive",
})((props) => ({
  position: "absolute",
  visibility: props.isActive ? "visible" : "hidden",
  width: "4px",
  top: 0,
  bottom: 0,
  left: 0,
  backgroundColor: (
    props.theme.palette as typeof props.theme.palette & {
      neutral: { light: string };
    }
  ).neutral.light,
  transition: "visibility 200ms",
  opacity: props.theme.palette.mode === "dark" ? 0.7 : 1,
}));

// ===========
// main
// ===========
export function RSidebarMuiItem({
  route,
  open,
  handleIsActive,
  activeListItem,
  handleDrawerClose,
  isMobile,
  fnIsAnimationStarted,
}: TComponentProps) {
  const routeId =
    route.to.indexOf("_") === -1
      ? route.to.slice(1)
      : route.to.slice(1, route.to.indexOf("_"));
  const dynamicTo = useAppSelector((state: any) =>
    navigateSelectById(state, routeId)
  );
  const isActive = activeListItem === route.id ? true : false;
  const navigate = useNavigate();

  // -----------
  // handlers
  // -----------
  const onMakeActive = (event: React.MouseEvent, routeId: string) => {
    event.stopPropagation();
    const DELAY = 0;

    window.setTimeout(() => {
      handleIsActive(routeId);
    }, DELAY);
  };

  const handleClick = (
    event: any,
    route: string,
    dynamicTo: string | undefined
  ) => {
    event.preventDefault();
    const DELAY = 0;

    window.setTimeout(() => {
      if (dynamicTo === undefined) {
        navigate(route);
      } else {
        navigate(dynamicTo);
      }

      if (isMobile) {
        handleDrawerClose();
      }
    }, DELAY);
  };

  // -----------
  // render
  // -----------
  return (
    <ListItem
      key={route.id}
      disablePadding
      sx={_stylesListItem()}
      onClick={(event) => onMakeActive(event, route.id)}
    >
      <StyledListItemButton
        className="Drawer_ListItemButton_Link"
        onClick={(event) => handleClick(event, route.to, dynamicTo?.active)}
        open={open}
      >
        <StyledListItemIcon className="Drawer_ListItemIcon" open={open}>
          <route.icon />
        </StyledListItemIcon>
        {
          <StyledListItemText
            className="Drawer_ListItemText"
            primary={route.title.toUpperCase()}
            open={open}
            fnIsAnimationStarted={fnIsAnimationStarted}
          />
        }
      </StyledListItemButton>
      <StyledDivider
        component="div"
        className="RSidebarMuiItem_Divider"
        isActive={isActive}
      />
    </ListItem>
  );
}

// ===========
// styles
// ===========
function _stylesListItem() {
  return {
    display: "block",
    position: "relative",
  };
}
