import * as React from "react";
/* app */
import { useMainContext } from "../context/MainContextProvider";

// =============
// type
// =============
type TScreenSize = <T extends number>(width: T, height: T) => void;

// =============
// const
// =============
const DELAY = 500;

// =============
// main
// =============
export function useScreenSize() {
  const { updateScreenSize } = useMainContext();

  React.useEffect(() => {
    const debounceHandleResize = _debounce(
      _screenSizeUpdater(updateScreenSize),
      DELAY
    );

    window.addEventListener("resize", debounceHandleResize);

    return () => {
      window.removeEventListener("resize", debounceHandleResize);
    };
  });
}

// -------------
// _debounce
// -------------
function _debounce(fn: Function, ms: number) {
  let timer: number | undefined;

  return () => {
    clearTimeout(timer);

    timer = window.setTimeout(() => {
      timer = undefined;
      fn();
    }, ms);
  };
}

// -------------
// _screenSizeUpdater
// -------------
function _screenSizeUpdater(updateScreenSize: TScreenSize) {
  return () => {
    updateScreenSize(window.innerWidth, window.innerHeight);
  };
}
