import Box from "@mui/material/Box";
/* app */
import { useMainContext } from "../../context/MainContextProvider";
import { themeSettings } from "../../theming/themeSettings";
import { RHeader } from "./page_header/RHeader";
import { ELangs } from "../../enums/ELangs";
import type { TColors } from "../../types/main-types";
import reference from "../../reference.json";
import { RHomePageWrapper } from "./home_page/RHomePageWrapper";

// =============
// types
// =============
export type TPageUtils = {
  colors: TColors;
  lang: ELangs;
};
type TComponentProps = {
  what: string & keyof typeof reference.greetings;
};

// =============
// main
// =============
export function RStartPage({ what }: TComponentProps) {
  const { theme, lang } = useMainContext();
  const colors = themeSettings(theme.palette.mode);

  const pageUtils = {
    colors,
    lang,
  };

  // -------------
  // rendering
  // -------------
  return (
    <Box sx={_boxStyles()} className="RStartPage">
      <RHeader title={what} utils={pageUtils} lang={lang} />
      <RHomePageWrapper lang={lang} />
    </Box>
  );
}

// =============
// styles
// =============
function _boxStyles() {
  return {
    display: "grid",
    gridTemplateRows: "min-content 1fr",
    overflowY: "auto",
  };
}
