// =============
// import
// =============
import * as bm from "./barrelModule";

// =============
// types
// =============
type TComponentProps = {
  lang: bm.ELangs;
};

// =============
// main
// =============
export function RHomePageWrapper(props: TComponentProps) {
  const lang = bm.useMemo(() => props.lang, [props.lang]);

  // blockStart------------------
  // const { theme } = bm.useMainContext();
  // const themeAlias = theme as Record<string, any>;

  // const matches = bm.useMediaQuery(() => themeAlias.breakpoints.up("sm"));
  // blockEnd--------------------

  // -------------
  // rendering
  // -------------
  return (
    <bm.Box className="RHomePageWrapper" sx={_wrappserStyle()}>
      <bm.RHomeTabs lang={lang} />
    </bm.Box>
  );
}

// =============
// styles
// =============
function _wrappserStyle() {
  const DESKTOP_WIDTH = "85%";
  const MOBILE_WIDTH = "78%";
  return {
    display: "grid",
    // gridTemplateColumns: `${matches ? DESKTOP_WIDTH : MOBILE_WIDTH} 1fr`,
    gridTemplateColumns: `minmax(${DESKTOP_WIDTH}, ${MOBILE_WIDTH}) 1fr`,
    minWidth: 0,
  };
}
