// =============
// import
// =============
import React from "react";
import { useRef } from "react";
import { ResponsiveLine } from "@nivo/line";
import Box from "@mui/material/Box";
import useMediaQuery from "@mui/material/useMediaQuery";
// import { SxProps, Theme } from "@mui/material/styles";
/* app */
import { type TECountriesVal } from "../../../../enums/ECountries";
import { type TEWhatVal } from "../../../../enums/EWhat";
import { useMainContext } from "../../../../context/MainContextProvider";
import { themeSettings } from "../../../../theming/themeSettings";
import config from "../../../../config.json";
import { ELangs } from "../../../../enums/ELangs";
import { pickCountryColor } from "../../../../func/prepare/pickCountryColor";
import { getThemeColors } from "../../../../func/prepare/getThemeColors";
import { RRangeSlider } from "../sliders/RRangeSlider";
import reference from "../../../../reference.json";
import { RStyledTooltipComponent } from "../../../../styled/RStyledTooltipComponent";
import { ETabsNames } from "../tabs/RBasicTabs";

// =============
// types
// =============
type TComponentProps = {
  data: Array<TData>;
  axisTitles: { x: string; y: string };
  lang: ELangs;
  minMaxY: { minY: number | null; maxY: number | null };
  target: TEWhatVal;
  xArray: Array<string> | null;
  xRange: Array<string> | null;
  handleChangedXRange: (range: [number, number]) => void;
  rangeTupple: [] | [number, number];
  baseHeight: string;
  setCurrentTab: (tab: ETabsNames) => void;
};
type TData = {
  id: TECountriesVal;
  data: Array<{ x: string; y: number }>;
  minX: string;
  maxX: string;
  stepX: number;
  minY: number;
  maxY: number;
  stepY: number;
};
// type TTooltipComponent = {
//   sx?: SxProps<Theme>;
//   tooltipObj: Record<string, any>;
//   cntrs: Record<string, string>;
//   className?: string;
// };

// =============
// main
// =============
export function RLine({
  data,
  axisTitles,
  minMaxY,
  target,
  xArray,
  xRange,
  handleChangedXRange,
  rangeTupple,
  lang,
  // baseHeight,
  setCurrentTab,
}: TComponentProps) {
  const { theme, isAnimation, saveTargetInContext } = useMainContext();

  const dataId =
    target +
    data
      .map((obj) => obj.id)
      .sort()
      .join("");

  const cntrs = data.reduce((resultObj, elem) => {
    return Object.assign({}, resultObj, {
      [elem.id]: reference.countries[elem.id].shortName[lang],
    });
  }, {});

  const oldDataId = useRef("");

  if (dataId !== oldDataId.current) {
    oldDataId.current = dataId;
  }

  const colors = pickCountryColor(data);
  const themeColors = getThemeColors(theme, themeSettings);
  const themeAlias = theme as { [x: string]: any };

  const matches = useMediaQuery(() => themeAlias.breakpoints.up("md"));
  const marginRight = matches
    ? Number(config.layout.charts.line.margin.right.md)
    : Number(config.layout.charts.line.margin.right.xs);

  const DIVIDER_DECKTOP = 2;
  const DIVIDER_MOBILE = 5;

  const X_NUM_FOR_CLOSE = 12;

  const resultXArray =
    xRange === null
      ? null
      : xRange.reduce((accum: Array<number>, year: string) => {
          const yearNumber = Number(year);
          if (matches && data.length > 0) {
            if (data[0].data.length <= X_NUM_FOR_CLOSE) {
              accum.push(yearNumber);
            } else if (yearNumber % DIVIDER_DECKTOP === 0) {
              accum.push(yearNumber);
            }
          } else {
            if (yearNumber % DIVIDER_MOBILE === 0) {
              accum.push(yearNumber);
            }
          }
          return accum;
        }, [] as Array<number>);

  // -------------
  // Effects
  // -------------
  React.useEffect(() => {
    saveTargetInContext(target);
  }, [target, saveTargetInContext]);

  React.useEffect(() => {
    setCurrentTab(ETabsNames.CHART);
  }, []);

  // -------------
  // rendering
  // -------------
  if (!isAnimation && data.length > 0) {
    return (
      <Box className="RLineWrapper" sx={_rLineWrapperStyle()}>
        <Box sx={_rlineStyles()} className="RLine">
          <LocalLine
            data={data}
            colorsOfCountries={colors}
            themeColors={themeColors}
            axisTitles={{ x: "", y: axisTitles.y }}
            marginRight={marginRight}
            matches={matches}
            minY={minMaxY.minY}
            maxY={minMaxY.maxY}
            tickValues={
              resultXArray === null || data.length === 0 ? [] : resultXArray
            }
            cntrs={cntrs}
          />
        </Box>
      </Box>
    );
  } else {
    return (
      <Box
        className="RLineWrapper"
        sx={_rLineWrapperStyle()}
        style={{ visibility: "hidden" }}
      >
        <Box sx={_rlineStyles()} className="RLine">
          <div></div>
        </Box>
        <span style={{ marginTop: "1em" }}>
          {axisTitles.x.toLocaleLowerCase()}
        </span>
        <RRangeSlider
          xArray={xArray}
          handleChangedXRange={handleChangedXRange}
          rangeTupple={rangeTupple}
          className="RLine"
          sx={{ width: "92%", marginTop: "1em" }}
        />
      </Box>
    );
  }
}

// =============
// subComponents
// =============
function LocalLine({
  data,
  colorsOfCountries,
  themeColors,
  axisTitles,
  marginRight,
  matches,
  minY,
  maxY,
  tickValues,
  cntrs,
}: any) {
  return (
    <ResponsiveLine
      colors={colorsOfCountries}
      theme={_nivoTheme(themeColors)}
      data={data}
      margin={{ top: 20, right: marginRight, bottom: 70, left: 60 }}
      xScale={{ type: "point" }}
      yScale={{
        type: "linear",
        min: minY === null ? "auto" : minY,
        max: maxY === null ? "auto" : maxY,
        // https://github.com/plouc/nivo/issues/557#issuecomment-493913356
        stacked: false,
        reverse: false,
      }}
      yFormat=" >-.2f"
      axisTop={null}
      axisRight={null}
      axisBottom={{
        tickSize: 15,
        tickPadding: 5,
        tickRotation: 0,
        tickValues: tickValues,
        legend: axisTitles.x,
        legendOffset: 55,
        legendPosition: "middle",
      }}
      axisLeft={{
        tickSize: 5,
        tickPadding: 5,
        tickRotation: 0,
        legend: axisTitles.y,
        legendOffset: -54,
        legendPosition: "middle",
      }}
      enablePoints={matches ? true : false}
      pointSize={10}
      pointColor={{ theme: "pointColor" }}
      pointBorderWidth={2}
      pointBorderColor={{ from: "serieColor" }}
      pointLabelYOffset={-12}
      useMesh={true}
      tooltip={(point) => (
        <RStyledTooltipComponent tooltipObj={point} cntrs={cntrs} />
      )}
    />
  );
}

// =============
// style
// =============
function _rlineStyles() {
  return {
    display: "flex",
    flexDirection: "column",
    width: "100%",
    height: "100%",
    justifyContent: "space-between",
    "& > div > div": {
      height: "100%",
    },
    "& text": {
      // dominantBaseline: "auto",
      fill: "red",
    },
  };
}

function _rLineWrapperStyle() {
  return {
    display: "flex",
    flexDirection: "column",
    justifyContent: "space-between",
    alignItems: "center",
    height: "100%",
  };
}

// -------------
// _nivoTheme
// -------------
function _nivoTheme(themeColors: { theme: any; colors: any }) {
  const mode = themeColors.theme.palette.mode;
  const colors = themeColors.colors.palette;
  const isDark = mode === "dark" ? true : false;

  const result = {
    background: "transparent",
    pointColor: colors.background.default,
    fontSize: 11,
    axis: {
      domain: {
        line: {
          stroke: isDark ? colors.neutral.dark : colors.white.dark,
          strokeWidth: 1,
        },
      },
      legend: {
        text: {
          fontSize: 12,
          fill: colors.text.primary,
        },
      },
      ticks: {
        line: {
          stroke: isDark ? colors.neutral.dark : colors.white.dark,
          strokeWidth: 1,
        },
        text: {
          fontSize: 11,
          fill: colors.text.primary,
        },
      },
    },
    grid: {
      line: {
        stroke: isDark
          ? colors.secondary.main + "A1"
          : colors.secondary.main + "4F",
        strokeWidth: 1,
      },
    },
    legends: {
      title: {
        text: {
          fontSize: 11,
          fill: "yellow",
        },
      },
      text: {
        fontSize: 12,
        fill: "yellow",
      },
      ticks: {
        line: {},
        text: {
          fontSize: 10,
          fill: "yellow",
        },
      },
    },
    annotations: {
      text: {
        fontSize: 13,
        fill: "yellow",
        outlineWidth: 2,
        outlineColor: "#ffffff",
        outlineOpacity: 1,
      },
      link: {
        stroke: isDark ? colors.lightShade.main : colors.darkShade.main,
        strokeWidth: 1,
        outlineWidth: 2,
        outlineColor: "#ffffff",
        outlineOpacity: 1,
      },
      outline: {
        stroke: "#000000",
        strokeWidth: 2,
        outlineWidth: 2,
        outlineColor: "#ffffff",
        outlineOpacity: 1,
      },
      symbol: {
        fill: "#000000",
        outlineWidth: 2,
        outlineColor: "#ffffff",
        outlineOpacity: 1,
      },
    },
    tooltip: {
      container: {
        background: "#ffffff",
        color: "#333333",
        fontSize: 12,
      },
      basic: {},
      chip: {},
      table: {},
      tableCell: {},
      tableCellValue: {},
    },
    crosshair: {
      line: {
        stroke: isDark
          ? colors.neutral.other["300"]
          : colors.neutral.other["900"],
        strokeWidth: 1,
      },
    },
  };

  return result;
}
