import * as React from "react";
import Switch from "@mui/material/Switch";
import Typography from "@mui/material/Typography";
import Stack from "@mui/system/Stack";
/* app */
import { ELangs } from "../../enums/ELangs";
import reference from "../../reference.json";

// =============
// types
// =============
type TChangeLang = (lang: ELangs) => void;
type TComponentProps = {
  colors: { palette: { [x: string]: any } };
  changeLang: (lang: ELangs) => void;
  lang: ELangs;
};
type TRChangeEvent = React.ChangeEvent<HTMLInputElement>;

// =============
// main
// =============
export function RTopbarSwitchLang({ lang, changeLang }: TComponentProps) {
  const langToBool = _langToBool;
  const handleChange = _handleChange(changeLang);

  return (
    <Stack className="RTopbarSwitchLang" direction="row" alignItems="center">
      <Typography>{reference.ui.lang.name.RU}</Typography>
      <Switch checked={langToBool(lang)} onChange={handleChange} />
      <Typography>{reference.ui.lang.name.EN}</Typography>
    </Stack>
  );
}

function _langToBool(lang: ELangs) {
  if (lang === ELangs.EN) {
    return true;
  }

  return false;
}

function _boolToLang(b: boolean) {
  if (b === true) {
    return ELangs.EN;
  }

  return ELangs.RU;
}

function _handleChange(changeLang: TChangeLang) {
  return function (event: TRChangeEvent) {
    const val = event.target.checked;
    const lang = _boolToLang(val);
    changeLang(lang);
  };
}
