// =========
// import
// =========
import * as bm from "./barrelModule";
import { type Theme } from "@mui/material/styles";

// =========
// main
// =========
export const WebKitScrollbar = (reactComponentName: any) =>
  bm.styled(reactComponentName)((propsAll) => {
    const { theme, ...propsOther } = propsAll;

    // ---------
    // render
    // ---------
    return Object.assign(
      {},
      {
        "&::-webkit-scrollbar": {
          width: "0.5rem",
          height: "0.5rem",
        },
        "&::-webkit-scrollbar-track": {
          borderRadius: "10px",
        },
        "&:hover::-webkit-scrollbar-track": {
          backgroundColor: theme.palette.darkAccent.main + "4A",
        },
        "&::-webkit-scrollbar-thumb": {
          borderRadius: "10px",
        },
        "&:hover::-webkit-scrollbar-thumb": {
          backgroundColor: theme.palette.lightShade.main + "4A",
        },
        "&::-webkit-scrollbar-corner": {
          opacity: 0,
        },
      },
      { ...propsOther }
    );
  });

// ==========
// function
// ==========
export const webKitScrollbarFn = (theme: Theme) => ({
  "&::-webkit-scrollbar": {
    width: "0.5rem",
    height: "0.5rem",
  },
  "&::-webkit-scrollbar-track": {
    borderRadius: "10px",
  },
  "&:hover::-webkit-scrollbar-track": {
    backgroundColor:
      (
        theme.palette as typeof theme.palette &
          Record<"darkAccent", { main: string }>
      ).darkAccent.main + "4A",
  },
  "&::-webkit-scrollbar-thumb": {
    borderRadius: "10px",
  },
  "&:hover::-webkit-scrollbar-thumb": {
    backgroundColor:
      (
        theme.palette as typeof theme.palette &
          Record<"lightShade", { main: string }>
      ).lightShade.main + "4A",
  },
  "&::-webkit-scrollbar-corner": {
    opacity: 0,
  },
});
