/**
 * type TGeneric = bm.TRline;
 */

// ============
// fetch dictionary
// ============
// let fetchGen = {} as Record<
//   string,
//   AsyncThunk<
//     {
//       countries: Array<TECountriesVal>;
//       data: Array<any>;
//       target: TEWhatVal;
//     },
//     bm.TFetchParams,
//     any
//   >
// >;

// ============
// slice
// ============
function genericSliceFactory(target: bm.TEWhatVal) {
  const fetchGen = { [`fetch_${target}`]: fetchGeneric(target) };
  // fetchGen = Object.assign({}, fetchGen, {
  //   [`fetch_${target}`]: fetchGeneric(target),
  // });

  // ---------
  // slice
  // ---------
  return bm.createSlice({
    name: target,
    initialState: genericAdapter.getInitialState<bm.TInitialState>({
      status: bm.EStatus.IDLE,
      minX: null,
      maxX: null,
      minY: null,
      maxY: null,
      stepX: null,
      stepY: null,
      error: null,
    }),
    reducers: {
      [`updateMinMaxYGeneric_${target}`](state, action) {
        const { minY, maxY } = action.payload;
        state.minY = minY;
        state.maxY = maxY;
      },
      [`updateMinMaxXGeneric_${target}`](state, action) {
        const { minX, maxX, stepX } = action.payload;
        state.minX = minX;
        state.maxX = maxX;
        state.stepX = stepX;
      },
    },
    extraReducers(builder) {
      builder.addCase(
        fetchGen[`fetch_${target}`].fulfilled,

        (state, action) => {
          const { countries, data } = action.payload;
          const result = countries.map((country, inx) =>
            bm.prepareData(country, data[inx])
          );
          state.status = bm.EStatus.SUCCEEDED;
          genericAdapter.upsertMany(state, result);
        }
      );
    },
  });
}

// ============
// reducer
// ============
export default genericSliceFactory;
