// ===========
// import
// ===========
import * as bm from "./barrelModule";
import type { TECountriesVal } from "../../../../enums/ECountries";
import { ELangs } from "../../../../enums/ELangs";
import { RStyledTooltipComponent } from "../../../../styled/RStyledTooltipComponent";
import { type TEWhatVal } from "./barrelModule";
import { ETabsNames } from "../tabs/RBasicTabs";

// =============
// types
// =============
type TComponentProps = {
  data: Array<TData>;
  xArray: Array<string> | null;
  xRange: Array<string> | null;
  handleChangedXRange: (range: [number, number]) => void;
  target: TEWhatVal;
  lang: ELangs;
  rangeTupple: [] | [number, number];
  dataID: string;
  baseHeight: string;
  setCurrentTab: (tab: ETabsNames) => void;
};
type TData = {
  id: TECountriesVal;
  data: Array<{ x: string; y: number }>;
  minX: string;
  maxX: string;
  stepX: number;
  minY: number;
  maxY: number;
  stepY: number;
};

// ===========
// main
// ===========
export function RMainResponsiveBar(props: TComponentProps) {
  const {
    xRange,
    data,
    // xArray,
    // handleChangedXRange,
    rangeTupple,
    dataID,
    // baseHeight,
  } = props;
  const target = props.target as bm.TEWhatVal;
  const lang = props.lang as bm.ELangs;

  // -----------
  // local store
  // -----------
  const localStore = bm.useRef<{
    isAction: boolean;
    oldXYear: number;
    isInit: boolean;
    oldRangeTupple: [] | [number, number];
    isManualChangedSlider: boolean;
    oldDataID: string;
  }>({
    isAction: false,
    oldXYear: 0,
    isInit: false,
    oldRangeTupple: [],
    isManualChangedSlider: false,
    oldDataID: "",
  });

  const isRangeTuppleChanged = (<T extends [] | [number, number]>(
    rangeTupple: T,
    oldRangeTupple: T
  ) => {
    return (
      rangeTupple[0] !== oldRangeTupple[0] ||
      rangeTupple[1] !== oldRangeTupple[1]
    );
  })(rangeTupple, localStore.current.oldRangeTupple);

  const isDataIDChanged = ((oldDataID: string, newDataID: string) => {
    return oldDataID !== newDataID;
  })(localStore.current.oldDataID, dataID);

  // -----------
  // local state
  // -----------
  const [oldTarget, setOldTarget] = bm.useState(target);
  const [readyData, setReadyData] = bm.useState<any | null>(null);
  const [xYear, setXYear] = bm.useState<number | Array<number>>(
    (() => {
      localStore.current.isInit = true;
      return 0;
    })()
  );

  const [_isActionChanged, setIsActionChanged] = bm.useState(false);

  if (oldTarget !== target) {
    setOldTarget(target);
  }

  // ---------
  // local state handlers
  // ---------
  const fnHandleIsAction = (isActionDraft: boolean) =>
    (localStore.current.isAction = isActionDraft);
  const isAction = () => localStore.current.isAction;

  // ---------
  // from context
  // ---------
  const { theme, isAnimation, saveTargetInContext } = bm.useMainContext();
  const themeColors = bm.getThemeColors(theme, bm.themeSettings);

  const isRangeTupple = rangeTupple.length > 0 ? true : false;
  const minX =
    xRange === null
      ? 0
      : isRangeTupple
      ? Number(rangeTupple[0])
      : Number(xRange[0]);
  const maxX =
    xRange === null
      ? 0
      : isRangeTupple
      ? Number(rangeTupple[1])
      : Number(xRange[xRange.length - 1]);
  const step = 1;

  // ----------
  // slider
  // ----------
  const startSlideYear =
    xRange === null || xYear === 0
      ? ""
      : xRange.lastIndexOf(String(xYear)) === xRange.length - 1
      ? xRange[0]
      : String(xYear);

  const yearGenerator =
    xRange === null
      ? { value: "", done: true }
      : _yearGenerator(xRange)(startSlideYear);

  // -----------
  // handlers
  // -----------
  const pseudoHandler = () => {
    const isRun = readyData === null ? false : true;

    if (isRun) {
      _runThumb(
        yearGenerator,
        setXYear,
        isAction,
        fnHandleIsAction,
        setIsActionChanged,
        true,
        localStore
      );
      fnHandleIsAction(true);
      setIsActionChanged((old) => !old);
    }
    return true;
  };

  const handlerSlideChange = (
    _event: Event,
    newValue: number | Array<number>
  ) => {
    setXYear((old: number | Array<number>) => {
      localStore.current.oldXYear = old as number;
      return newValue;
    });
  };

  const stopSlider = () => {
    fnHandleIsAction(false);
    setIsActionChanged((old) => !old);
  };

  // -----------
  // data for rendering
  // -----------
  let dataForRender = null;
  try {
    if (readyData === null) {
      dataForRender = null;
    } else {
      dataForRender = readyData[String(xYear)];
      if (dataForRender === undefined) {
        throw new Error(`xYear ${xYear} is out of xRange!`, { cause: "E_002" });
      }
    }
  } catch (error: any) {
    switch (error.cause) {
      case "E_002": {
        localStore.current.isAction = false;
        try {
          dataForRender = readyData[String(localStore.current.oldXYear)];
          if (dataForRender === undefined) {
            throw new Error(`xYear ${xYear} is out of xRange`, {
              cause: "E_002_02",
            });
          }
          setXYear(localStore.current.oldXYear);
        } catch (error2) {
          const readyDataKeysArr = Object.keys(readyData);
          const lastReadyDataYear = Number(
            readyDataKeysArr[readyDataKeysArr.length - 1]
          );
          dataForRender = readyData[String(lastReadyDataYear)];
          setXYear(() => {
            localStore.current.oldXYear = lastReadyDataYear as number;
            return lastReadyDataYear;
          });
        }
        break;
      }

      default:
        dataForRender = readyData[String(localStore.current.oldXYear)];
        break;
    }
  }

  const axisYLegend = bm.reference.targets[target].y[lang];
  // const rangeXName = bm.reference.targets[target].x[lang];
  const axisXLegend = bm.reference.targets[target].xBars[lang];

  // -----------
  // options for rendering
  // -----------
  const themeAlias = theme as { [x: string]: any };
  const matches = bm.useMediaQuery(() => themeAlias.breakpoints.up("md"));
  const marginRight = matches
    ? Number(bm.config.layout.charts.line.margin.right.md)
    : Number(bm.config.layout.charts.line.margin.right.xs);

  // -----------
  // effects
  // -----------
  bm.useEffect(() => {
    // data
    const dataArray: null | Array<{
      year: string;
      chartData: Array<{ id: string; y: number }>;
    }> =
      xRange === null
        ? null
        : xRange.map((year: string) => {
            return {
              year: year,
              chartData: data
                .map((obj: any) => {
                  return {
                    id: obj.id,
                    y: Math.round(
                      obj.data.find((element: any) => element.x === year).y
                    ),
                  };
                })
                .sort((a: { y: number }, b: { y: number }) => {
                  return b.y > a.y ? 1 : -1;
                }),
            };
          });

    // ---------
    // colors
    // ---------
    const dataWithColors =
      dataArray === null
        ? null
        : (_addColors(dataArray) as Array<{
            year: string;
            chartData: Array<{ id: string; color: string; y: number }>;
          }>);

    // ---------
    // shortnames
    // ---------
    const dataWithShortNames =
      dataArray === null
        ? null
        : _addShortNames(
            dataWithColors as Array<{
              year: string;
              chartData: Array<{ id: string; color: string; y: number }>;
            }>,
            lang
          );

    // ---------
    // result data
    // ---------
    if (dataWithShortNames !== null) {
      const resultData = dataWithShortNames.reduce((accum, obj) => {
        const newObj = { [obj.year]: obj };
        return { ...accum, ...newObj };
      }, {} as Record<string, any>);

      // ------
      // set data
      // ------
      setReadyData(resultData);

      // ------
      // set the end year
      // ------
      if (rangeTupple.length > 0) {
        if (
          (localStore.current.isAction && localStore.current.isInit) ||
          (localStore.current.isInit && isDataIDChanged)
        ) {
          // nothing
        } else {
          setXYear((old: number | Array<number>) => {
            localStore.current.oldXYear = old as number;
            return rangeTupple[1] as number;
          });
        }
      } else if (xRange !== null) {
        if (isDataIDChanged) {
          localStore.current.oldDataID = dataID;
        } else {
          setXYear((old: number | Array<number>) => {
            localStore.current.oldXYear = old as number;
            return parseInt(xRange[xRange.length - 1]);
          });
        }
      }

      // ---------
      // turn off 'init' status
      // ---------
      if (localStore.current.isInit) {
        localStore.current.isInit = false;
        localStore.current.oldRangeTupple = rangeTupple;
      }

      // ---------
      // to default
      // ---------
      localStore.current.isManualChangedSlider = false;
    }
  }, [target, xRange, lang]);

  bm.useEffect(() => {
    saveTargetInContext(target);
  }, [target]);

  bm.useEffect(() => {
    props.setCurrentTab(ETabsNames.BAR);
  }, []);

  // unmount
  bm.useEffect(() => () => {
    localStore.current.isInit = false;
  });

  // ------
  // Slider value
  // ------
  const thumbYear = isRangeTuppleChanged ? maxX : xYear;

  // -----------
  // rendering
  // -----------
  if (!isAnimation && dataForRender !== null) {
    return (
      <bm.Box
        className="RMainResponsiveBarWrapper"
        sx={_rResponsiveBarWrapperStyle()}
      >
        <bm.Box className="RMainResponsiveBar" sx={_mainResponsiveBarStyle()}>
          <Bar
            className="Bar"
            data={dataForRender ? dataForRender.chartData : null}
            themeColors={themeColors}
            marginRight={marginRight}
            axisXLegend={axisXLegend}
            axisYLegend={axisYLegend}
          />
        </bm.Box>
        <bm.Box className="BarSliderBox" sx={_barSliderBoxStyle()}>
          {isAction() === false ? (
            <bm.iconsStore.Play onClick={pseudoHandler} />
          ) : (
            <bm.iconsStore.Pause onClick={stopSlider} />
          )}
          <bm.Slider
            aria-labelledby="input-slider"
            className="Slider"
            min={minX}
            max={maxX}
            step={step}
            value={Number(thumbYear)}
            onChange={handlerSlideChange}
            valueLabelDisplay="on"
            sx={_inputSliderStyle(themeColors)}
          />
        </bm.Box>
      </bm.Box>
    );
  } else {
    return (
      <bm.Box
        className="RMainResponsiveBarWrapper"
        sx={_rResponsiveBarWrapperStyle()}
        style={{ visibility: "hidden" }}
      >
        <bm.Box className="RMainResponsiveBar" sx={_mainResponsiveBarStyle()}>
          {/* Empty "HTMLDivElement" */}
          <div></div>
        </bm.Box>
      </bm.Box>
    );
  }
}

// ===========
// subcomponents
// ===========
function Bar(props: any) {
  const { data, themeColors, marginRight, axisXLegend, axisYLegend } = props;
  const countries = data.map(
    (dataObj: Record<"shortName", string>) => dataObj.shortName
  );

  // -----------
  // rendering
  // -----------
  return (
    <bm.ResponsiveBar
      data={data}
      keys={countries}
      theme={_nivoTheme(themeColors)}
      indexBy="shortName"
      margin={{ top: 50, right: marginRight, bottom: 60, left: 70 }}
      padding={0.3}
      valueScale={{ type: "linear" }}
      indexScale={{ type: "band", round: true }}
      colors={({ id, data }) => String(data[`${id}Color`])}
      borderColor={{
        from: "color",
        modifiers: [["darker", 1.6]],
      }}
      axisTop={null}
      axisRight={null}
      axisBottom={{
        tickSize: 5,
        tickPadding: 5,
        tickRotation: 0,
        legend: axisXLegend,
        legendPosition: "middle",
        legendOffset: 42,
      }}
      axisLeft={{
        tickSize: 5,
        tickPadding: 5,
        tickRotation: 0,
        legend: axisYLegend,
        legendPosition: "middle",
        legendOffset: -60,
      }}
      labelSkipWidth={12}
      labelSkipHeight={12}
      labelTextColor={{
        from: "color",
        modifiers: [["darker", 1.6]],
      }}
      role="application"
      ariaLabel="Nivo bar chart"
      barAriaLabel={function (e) {
        return e.id + ": " + e.formattedValue + " in country: " + e.indexValue;
      }}
      enableLabel={false}
      tooltip={(point) => <RStyledTooltipComponent tooltipObj={{ point }} />}
    />
  );
}

// ============
// utils
// ============
function _addColors(
  dataArray: Array<{
    year: string;
    chartData: Array<{ id: string; y: number }>;
  }>
) {
  const countries = Object.values(bm.ECountries);
  const countriesObj = bm.reference.countries;
  const colorsArray = countries.map((countryId: bm.TECountriesVal) => ({
    id: countryId,
    color: countriesObj[countryId].colors.main,
  }));

  const newData = dataArray.map((dataTopObj) => ({
    ...dataTopObj,
    chartData: dataTopObj.chartData.map((dataObj: any) => {
      const foundedColorObject = colorsArray.find(
        (colorObj) => colorObj.id === dataObj.id
      );

      if (foundedColorObject === undefined) {
        throw new Error(
          "RMainResponsiveBar::_addColors::[ERROR]: 'searchedObj' is not founded."
        );
      }

      return {
        ...dataObj,
        color: foundedColorObject.color,
      };
    }),
  }));

  return newData;
}

function _nivoTheme(themeColors: { theme: any; colors: any }) {
  const mode = themeColors.theme.palette.mode;
  const colors = themeColors.colors.palette;
  const isDark = mode === "dark" ? true : false;

  return {
    textColor: colors.text.primary,
    ticks: {
      line: {
        stroke: "yellow",
        strokeWidth: 1,
      },
      text: {
        fontSize: 11,
        // fill: isDark ? colors.neutral.main : colors.neutral.dark,
        // fill: colors.text.primary,
      },
    },
    grid: {
      line: {
        stroke: isDark
          ? colors.secondary.main + "A1"
          : colors.secondary.main + "4F",
        strokeWidth: 1,
      },
    },
  };
}

function _addShortNames(
  data: Array<{
    year: string;
    chartData: Array<{ id: string; color: string; y: number }>;
  }>,
  lang: bm.ELangs
) {
  const countriesObj = bm.reference.countries;
  return data.map((obj) => {
    const chartData = obj.chartData.map((chart) => {
      const name =
        countriesObj[chart.id as keyof typeof countriesObj].shortName[lang];
      return {
        ...chart,
        shortName: name,
        [name]: chart.y,
        [`${name}Color`]: chart.color,
      };
    });

    return { ...obj, chartData };
  });
}

function _yearGenerator(xRange: Array<string>) {
  return function* (year: string) {
    const index = xRange.indexOf(String(year));
    const start = index === 0 ? 0 : index + 1;
    for (const year of xRange.slice(start)) {
      yield year;
    }
  };
}

function _runThumb(
  yearGenerator: any,
  setXYear: any,
  isAction: Function,
  fnHandleIsAction: Function,
  setIsActionChanged: Function,
  start: boolean = true,
  localStore: bm.React.MutableRefObject<{ isAction: boolean; oldXYear: number }>
) {
  const showTime = start ? 0 : 2000;
  const nextYear = yearGenerator.next();

  if (nextYear.done) {
    fnHandleIsAction(false);
    setIsActionChanged((old: boolean) => !old);
  } else {
    const timer = window.setTimeout(() => {
      if (isAction()) {
        setXYear((old: number) => {
          localStore.current.oldXYear = old;
          return nextYear.value;
        });
        _runThumb(
          yearGenerator,
          setXYear,
          isAction,
          fnHandleIsAction,
          setIsActionChanged,
          false,
          localStore
        );
      } else {
        window.clearTimeout(timer);
      }
    }, showTime);
  }
}

// ============
// styles
// ============
function _barSliderBoxStyle() {
  return {
    width: "95%",
    display: "grid",
    gridTemplateColumns: "max-content 1fr",
    gap: "1rem",
    alignItems: "center",
    cursor: "pointer",
    marginLeft: "0.4em",
  };
}

function _inputSliderStyle<T extends Record<"theme" | "colors", any>>(
  props: T
) {
  const TRACK_COLOR = props.theme.palette.white.light;
  const DONE_COLOR = "red";
  const THUMB_DIMENSIONS = "20px";

  return {
    "& .MuiSlider-track": {
      backgroundColor: DONE_COLOR,
      border: 0,
    },
    "& .MuiSlider-thumb": {
      width: THUMB_DIMENSIONS,
      height: THUMB_DIMENSIONS,
      backgroundColor: DONE_COLOR,
      borderRadius: "50%",
    },
    "& .MuiSlider-rail": {
      backgroundColor: TRACK_COLOR,
    },
  };
}

function _mainResponsiveBarStyle() {
  return {
    display: "flex",
    flexDirection: "column" as const,
    height: "90%",
    width: "100%",
    justifyContent: "space-between",
    // alignItems: "center",
    "& > div > div": {
      height: "100%",
    },
  };
}

function _rResponsiveBarWrapperStyle() {
  return {
    display: "flex",
    flexDirection: "column",
    justifyContent: "space-between",
    alignItems: "center",
    height: "100%",
  };
}
