// =============
// Import
// =============
import { colorsKit } from "./colorsKit";
import { fonts } from "./fonts";

// =============
// themeSettings
// =============
export function themeSettings(mode: "dark" | "light") {
  // const colors = colorsKit(mode);
  const colors = colorsKit();

  return {
    palette: {
      mode,
      ...(mode === "dark"
        ? {
            primary: {
              main: colors.mainBrand["DEFAULT"],
              // main: colors.gray["400"],
              other: colors.mainBrand,
            },
            secondary: {
              main: colors.lightAccent["DEFAULT"],
              dark: colors.lightAccent["800"],
              light: colors.lightAccent["100"],
              other: colors.lightAccent,
            },
            lightAccent: {
              main: colors.lightAccent["DEFAULT"],
              dark: colors.lightAccent["800"],
              other: colors.lightAccent,
            },
            lightShade: {
              main: colors.lightShade["DEFAULT"],
              other: colors.lightShade,
            },
            darkAccent: {
              main: colors.darkAccent["DEFAULT"],
              other: colors.darkAccent,
            },
            darkShade: {
              main: colors.darkShade["DEFAULT"],
              other: colors.darkShade,
            },
            neutral: {
              main: colors.grey["500"],
              dark: colors.grey["800"],
              light: colors.grey["200"],
              other: colors.grey,
            },
            white: {
              main: colors.white["600"],
              dark: colors.white["200"],
              light: colors.white["900"],
              other: colors.white,
            },
            background: {
              default: colors.darkShade["DEFAULT"],
              opposit: colors.darkAccent["DEFAULT"],
            },
            text: {
              primary: colors.grey["400"],
              // primary: "red",
              secondary: colors.grey["600"],
              // secondary: "green",
              disabled: colors.mainBrand["600"],
            },
            divider: colors.grey["800"],
          }
        : {
            primary: {
              main: colors.mainBrand["DEFAULT"],
              other: colors.mainBrand,
            },
            secondary: {
              main: colors.lightAccent["DEFAULT"],
              dark: colors.lightAccent["800"],
              light: colors.lightAccent["400"],
              other: colors.lightAccent,
            },
            lightAccent: {
              main: colors.lightAccent["DEFAULT"],
              other: colors.lightAccent,
            },
            lightShade: {
              main: colors.lightShade["DEFAULT"],
              other: colors.lightShade,
            },
            darkAccent: {
              main: colors.darkAccent["DEFAULT"],
              other: colors.darkAccent,
            },
            darkShade: {
              main: colors.darkShade["DEFAULT"],
              other: colors.darkShade,
            },
            neutral: {
              main: colors.grey["DEFAULT"],
              dark: colors.grey["800"],
              light: colors.grey["300"],
              other: colors.grey,
            },
            white: {
              main: colors.white["600"],
              dark: colors.white["200"],
              light: colors.white["900"],
              other: colors.white,
            },
            background: {
              // default: colors.lightShade["DEFAULT"],
              default: colors.white["500"],
              opposit: colors.lightAccent["DEFAULT"],
            },
            text: {
              primary: colors.grey["900"],
              secondary: colors.grey["700"],
              disabled: colors.grey["200"],
            },
            divider: colors.grey["700"],
          }),
    },
    typography: fonts,
    // status: "",
    // customNode: "",
  };
}

// =============
// types
// =============
export type TThemeSettings = typeof themeSettings;
