// eslint-disable-next-line import/no-anonymous-default-export
export function chartWrapperWorker(props: Record<string, Function>) {
  const {
    prepareAxisTitles,
    getActiveCountries,
    serializedDataIds,
    getLineMinMax,
    prepareXArray,
  } = props;

  // ----------
  // get message
  // ----------
  /* eslint-disable-next-line no-restricted-globals */
  self.onmessage = (evnt: MessageEvent<any>) => {
    // console.info(
    //   "%cchartWrapperWorker::evnt",
    //   "color: hotpink",
    //   JSON.stringify(evnt)
    // );
    const data = Object.assign({}, evnt.data);
    switch (data.message) {
      case "test": {
        const objToExport = {
          axisTitles: {} as Record<string, string>,
          data: [] as Array<Record<string, any>>,
          isData: false,
          isDataChanged: false,
          isTargetChanged: false,
          isRangeXChanged: false,
          oldData: "",
          oldTarget: "",
          minMaxY: { minY: null, maxY: null } as {
            minY: number | null;
            maxY: number | null;
          },
          minMaxX: { minX: null, maxX: null, stepX: null } as {
            minX: number | null;
            maxX: number | null;
            stepX: number | null;
          },
          xArray: null as Array<string> | null,
          xRange: null as Array<string> | null,
        };

        objToExport.axisTitles = prepareAxisTitles(
          data.payload.target,
          data.payload.lang,
          data.payload.reference
        ) as Record<string, string>;

        objToExport.data = getActiveCountries(
          data.payload.dataRedux,
          data.payload.countriesDic
        ) as Array<Record<string, any>>;

        objToExport.isData = objToExport.data.length > 0 ? true : false;

        const dataSerialized = serializedDataIds(objToExport.data);
        if (dataSerialized !== data.payload.oldData) {
          objToExport.isDataChanged = true;
        }
        if (data.payload.target !== data.payload.oldTarget) {
          objToExport.isTargetChanged = true;
        }

        objToExport.oldData = dataSerialized;
        objToExport.oldTarget = data.payload.target;

        // xArray
        const cntr1XDataArr =
          objToExport.data[0] === undefined
            ? []
            : objToExport.data[0].data
                .map((cntrDataObj: { x: string; y: number }) => cntrDataObj.x)
                .sort();
        const xArrayMinMax =
          cntr1XDataArr.length === 0
            ? { min: 0, max: 0, step: 0 }
            : {
                min: parseInt(cntr1XDataArr[0]),
                max: parseInt(cntr1XDataArr[cntr1XDataArr.length - 1]),
                step: parseInt(cntr1XDataArr[1]) - parseInt(cntr1XDataArr[0]),
              };

        objToExport.xArray =
          cntr1XDataArr.length === 0 ? [] : prepareXArray()(xArrayMinMax);

        // xRange
        if (
          data.payload.rangeXMin !== null &&
          (data.payload.isRangeTuppleChanged ||
            objToExport.isDataChanged ||
            objToExport.isTargetChanged)
        ) {
          const newData = objToExport.data.map((countryObj: any) => {
            countryObj.data = countryObj.data.filter(
              (dataObj: { x: string; y: number }) => {
                return (
                  Number(dataObj.x) >= data.payload.rangeXMin &&
                  Number(dataObj.x) <= data.payload.rangeXMax
                );
              }
            );
            return countryObj;
          });

          objToExport.isRangeXChanged = true;
          objToExport.data = newData;
        }

        objToExport.minMaxY = getLineMinMax()(objToExport.data, "y");
        objToExport.minMaxX = getLineMinMax()(objToExport.data, "x");

        // if active_countries.length === 0
        objToExport.xRange = objToExport.isData
          ? prepareXArray()(objToExport.minMaxX)
          : data.payload.xRange;
        objToExport.xArray = objToExport.isData
          ? objToExport.xArray
          : data.payload.xArray;

        // ---------
        // send message
        // ---------
        /* eslint-disable-next-line no-restricted-globals */
        self.postMessage({
          message: "succeeded",
          payload: objToExport,
        });
        break;
      }

      default: {
        if (process.env.NODE_ENV === "development") {
          console.info("%cchartWrapperWorker::PROBLEM: ", "color: red");
        }
        break;
      }
    }
  };
}
