// ============
// import
// ============
import * as bm from "../barrelModule";

// ============
// adapter
// ============
export const navigateAdapter = bm.createEntityAdapter<bm.TNavigateRoute>({
  sortComparer: (a, b) => a.id.localeCompare(b.id),
});

// ============
// selectors
// ============
export const { selectAll, selectById } = navigateAdapter.getSelectors(
  (state: bm.RootState) => state.navigate
);

