// ===========
// types
// ===========
type TComponentProps = {
  open: boolean;
  handleDrawerOpen: () => void;
  handleDrawerClose: () => void;
};
type TListItemButton = {
  className: string;
  children: Array<React.ReactElement>;
  onClick: () => void;
  open: boolean;
};
type TUniversalDrawer = {
  open?: boolean;
  handleDrawerClose: () => void;
  handleDrawerOpen: () => void;
  Close: OverridableComponent<SvgIconTypeMap<{}, "svg">> & {
    muiName: string;
  };
  Menu: OverridableComponent<SvgIconTypeMap<{}, "svg">> & {
    muiName: string;
  };
  routes: Array<TSidebarRoute>;
  handleIsActive: ([param]: string) => void;
  activeListItem: string;
  fnIsAnimationStarted: () => boolean;
};

// ===========
// const
// ===========
const drawerWidth = 240;
// const TITLE = "close".toLowerCase();

// ===========
// styles mixins
// ===========
const openedMixin = (theme: Theme): CSSObject => ({
  width: drawerWidth,
  transition: theme.transitions.create("width", {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.enteringScreen,
  }),
  overflow: "hidden",
});

const closedMixin = (theme: Theme): CSSObject => {
  return {
    transition: theme.transitions.create("width", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    overflowX: "hidden",
    width: `calc(${theme.spacing(7)} + 1px)`,
    //@ts-ignore
    [theme.breakpoints.up("sm")]: {
      width: `calc(${theme.spacing(8)} + 1px)`,
    },
  };
};

