const StyledMiniDrawer = styled(MuiDrawer, {
  shouldForwardProp: (prop) => {
    return prop !== "open";
  },
})((props) => {
  const { theme, open } = props;
  return {
    width: drawerWidth,
    flexShrink: 0,
    boxSizing: "border-box",
    ...(open && {
      ...openedMixin(theme),
      "& .MuiDrawer-paper": openedMixin(theme),
    }),
    ...(!open && {
      ...closedMixin(theme),
      "& .MuiDrawer-paper": closedMixin(theme),
    }),
    "& .MuiPaper-root": {
      backgroundColor: (
        theme.palette.background as typeof theme.palette.background & {
          opposit: string;
        }
      ).opposit,
      color: "inherit",
    },
    color: (
      theme.palette as typeof theme.palette & {
        neutral: {
          other: Record<string, any>;
          light: string;
        };
      }
    ).neutral.light,
    // color: "red",
    "& .MuiTypography-root": {
      letterSpacing: "0.1em",
    },
  };
});

const StyledDrawer = styled(Drawer)((props) => {
  const { theme, open } = props;
  return {
    width: drawerWidth,
    flexShrink: 0,
    boxSizing: "border-box",
    ...(open && {
      ...openedMixin(theme),
      "& .MuiDrawer-paper": openedMixin(theme),
    }),
    ...(!open && {
      ...closedMixin(theme),
      "& .MuiDrawer-paper": closedMixin(theme),
    }),
    "& .MuiPaper-root": {
      backgroundColor: (
        theme.palette.background as typeof theme.palette.background & {
          opposit: string;
        }
      ).opposit,
      color: "inherit",
    },
    color: (
      theme.palette as typeof theme.palette & {
        neutral: {
          other: Record<string, any>;
          light: string;
        };
      }
    ).neutral.light,
    // color: "red",
    "& .MuiTypography-root": {
      letterSpacing: "0.1em",
    },
  };
});

const StyledListItemButton = styled(
  (props: TListItemButton) => (
    <ListItemButton {...props}>{props.children}</ListItemButton>
  ),
  {
    shouldForwardProp: (prop) => prop !== "open",
  }
)((props) => {
  const PL = "4.108px"; //   
  return {
    height: "48px",
    justifyContent: "flex-start",
    px: 2.5,
    "& div": {
      color: "inherit",
    },
    "& .MuiListItemIcon-root": {
      paddingLeft: PL,
      opacity: props.theme.palette.mode === "dark" ? 0.7 : 1,
    },
  };
});

// ===========
// subcomponents
// ===========
function MenuList(
  props: Omit<
    TUniversalDrawer,
    "onAnimationStart" | "onAnimationIteration" | "onAnimationEnd"
  > & { isMobile: boolean }
) {
  return (
    <>
      {/* List Header */}
      <List>
        <ListItem disablePadding sx={{ display: "block" }} className="Menu">
          <StyledListItemButton
            className="RMiniDrawer_Box_Menu"
            onClick={
              props.open ? props.handleDrawerClose : props.handleDrawerOpen
            }
            open={props.open as boolean}
          >
            <ListItemIcon sx={_stylesListItemIcon(props.open as boolean)}>
              {props.open ? <props.Close /> : <props.Menu />}
            </ListItemIcon>
            <ListItemText
              className="RMiniDrawer_ListItemText"
              primary={/*TITLE*/ ""}
              sx={_stylesListItemText(props.open as boolean)}
            />
          </StyledListItemButton>
        </ListItem>
      </List>
      {/* List Body */}
      <List>
        {props.routes.map((route) => {
          return (
            <RSidebarMuiItem
              key={route.id}
              open={props.open as boolean}
              route={route}
              handleIsActive={props.handleIsActive}
              activeListItem={props.activeListItem}
              handleDrawerClose={props.handleDrawerClose}
              isMobile={props.isMobile}
              fnIsAnimationStarted={props.fnIsAnimationStarted}
            />
          );
        })}
      </List>
    </>
  );
}

function UniversalDrawer(props: TUniversalDrawer) {
  const { theme } = useMainContext();
  const isWiderThanMobileScreen = useMediaQuery(theme.breakpoints.up("sm"));

  const sharedComponent = (isMobile: boolean) => (
    <MenuList
      open={props.open}
      handleDrawerClose={props.handleDrawerClose}
      handleDrawerOpen={props.handleDrawerOpen}
      Close={props.Close}
      Menu={props.Menu}
      routes={props.routes}
      handleIsActive={props.handleIsActive}
      activeListItem={props.activeListItem}
      isMobile={isMobile}
      fnIsAnimationStarted={props.fnIsAnimationStarted}
    />
  );

  // ----------
  // rendering
  // ----------
  if (isWiderThanMobileScreen) {
    return (
      <StyledMiniDrawer variant="permanent" open={props.open}>
        {sharedComponent(false)}
      </StyledMiniDrawer>
    );
  } else {
    return (
      <StyledDrawer open={props.open} className="RMiniDrawer_Drawer">
        {sharedComponent(true)}
      </StyledDrawer>
    );
  }
}

// ===========
// main
// ===========
export function RMiniDrawer(props: TComponentProps) {
  // --------
  // local state
  // --------
  const [activeListItem, setActiveListItem] = React.useState("00");
  const [isAnimationEnd, setIsAnimationEnd] = React.useState(true);

  const { Menu, Close } = iconsStore;
  const routes = useItemsStack();
  const { changeIsAnimation } = useMainContext();

  // ---------
  // router info
  // ---------
  const routerPathname = useLocation().pathname;

  // ---------
  // local store
  // ---------
  const localStore = React.useRef({
    isDrawerClicked: false as boolean,
    isAnimationStarted: false as boolean,
  });

  // ---------
  // hendlers
  // ---------
  const handleIsActive = (activeListItemId: string) => {
    setActiveListItem(activeListItemId);
  };

  //     
  // AnimationEnd.  /
  // ,   200ms animationEnd 
  // 
  const fallbackAnimationEnd = () => {
    const delay = 200;
    window.setTimeout(() => {
      if (localStore.current.isAnimationStarted) {
        handleAnimationEnd();
      }
    }, delay);
  };

  const handleAnimationStart = (_e?: React.AnimationEvent<HTMLDivElement>) => {
    localStore.current.isAnimationStarted = true;
  };

  const handleAnimationEnd = (_e?: React.AnimationEvent<HTMLDivElement>) => {
    if (
      localStore.current.isAnimationStarted &&
      localStore.current.isDrawerClicked
    ) {
      localStore.current.isAnimationStarted = false;
      localStore.current.isDrawerClicked = false;
      changeIsAnimation(false);
      setIsAnimationEnd(true);
    }
  };

  const handleLocalDrawerOpen = () => {
    if (localStore.current.isAnimationStarted) {
      localStore.current.isDrawerClicked = true;
      changeIsAnimation(true);
      setIsAnimationEnd(false);
      props.handleDrawerOpen();
      fallbackAnimationEnd();
    }
  };
  const handleLocalDrawerClose = () => {
    if (localStore.current.isAnimationStarted) {
      localStore.current.isDrawerClicked = true;
      changeIsAnimation(true);
      setIsAnimationEnd(false);
      props.handleDrawerClose();
      fallbackAnimationEnd();
    }
  };

  // ---------
  // effects
  // ---------
  React.useEffect(() => {
    const regex = /^\/[a-z]+/;
    const mainRouteArray = routerPathname.match(regex);
    let mainRoute = "";

    if (mainRouteArray === null) {
      if (routerPathname === "/") {
        mainRoute = "/";
      } else {
        throw new Error("RMiniDrawer::useEffect: 'mainRoute === null.");
      }
    }

    mainRoute =
      mainRoute === "/" ? mainRoute : (mainRouteArray as Array<string>)[0];

    let initRouteObject = routes.find((routeObj) =>
      routeObj.to.startsWith(mainRoute)
    );

    if (
      process.env.NODE_ENV === "development" &&
      initRouteObject === undefined
    ) {
      initRouteObject = { id: "00" } as TSidebarRoute;
    }

    if (initRouteObject === undefined) {
      throw new Error(
        "RMiniDrawer::useEffect: 'initRouteObject === undefined."
      );
    }

    setActiveListItem(initRouteObject.id);
    // eslint-disable-next-line
  }, []);

  // ---------
  // render
  // ---------
  return (
    <Box
      className="RMiniDrawer_Box"
      sx={_stylesBox}
      onAnimationStart={(e: React.AnimationEvent<HTMLDivElement>) =>
        handleAnimationStart(e)
      }
      onAnimationEnd={(e: React.AnimationEvent<HTMLDivElement>) =>
        handleAnimationEnd(e)
      }
    >
      <UniversalDrawer
        open={props.open}
        handleDrawerClose={handleLocalDrawerClose}
        handleDrawerOpen={handleLocalDrawerOpen}
        Close={Close}
        Menu={Menu}
        routes={routes}
        handleIsActive={handleIsActive}
        activeListItem={activeListItem}
        fnIsAnimationStarted={() => !isAnimationEnd}
      />
    </Box>
  );
}

// ===========
// styles
// ===========
function _stylesListItemIcon(open: boolean) {
  return {
    minWidth: 0,
    mr: open ? 3 : "auto",
    justifyContent: "center",
    "& .MuiSvgIcon-root": {
      width: "1.5rem",
      height: "1.5rem",
    },
  };
}

function _stylesListItemText(open: boolean) {
  return {
    opacity: open ? 1 : 0,
    whiteSpace: "nowrap",
    transition: "opacity 0.01s 2s",
  };
}

function _stylesBox() {
  return {
    display: "flex",
  };
}
