// ===========
// import
// ===========
import * as bm from "./barrelModule";
import { useMemo } from "react";
import { RDescription } from "../description/RDescription";
import { RStyledHorizontalTabsWrap as StyledTabsWrap } from "../../../../styled/RStyledHorizontalTabsWrap";
import { RStyledHorizontalTabs as StyledTabs } from "../../../../styled/RStyledHorizontalTabs";
import { type SxProps, type Theme } from "@mui/material";

// ===========
// types
// ===========
type TComponentProps = {
  data: any;
  axisTitles: any;
  lang: bm.ELangs;
  minMaxY: any;
  xArray: any;
  xRange: any;
  target: any;
  handleChangedXRange: (range: [number, number]) => void;
  rangeTupple: [] | [number, number];
  dataID: string;
  fnSetCurrentTabName: (tabName: ETabsNames) => void;
};
export enum ETabsNames {
  CHART = "charts",
  BAR = "bars",
  TABLE = "table",
  DESCRIPTION = "description",
  SOURCE = "source",
}

// =========
// constants
// =========
const BASE_HEIGHT = "50vh";

// ===========
// main
// ===========
export function RBasicTabs(props: TComponentProps) {
  const [value, setValue] = bm.useState(0);

  const {
    data,
    axisTitles,
    lang,
    minMaxY,
    xArray,
    xRange,
    target,
    handleChangedXRange,
    rangeTupple,
    dataID,
  } = props;

  // -----------
  // local store
  // -----------
  // const localStore = bm.React.useRef({
  //   currentTab: ETabsNames.CHART as ETabsNames,
  // });

  // const setLsCurrentTab = (activeTab: ETabsNames) => {
  //   localStore.current.currentTab = activeTab;
  // };

  // -----------
  // handlers
  // -----------
  const handleChange = (_event: bm.React.SyntheticEvent, newValue: number) => {
    setValue(newValue);
  };

  // -----------
  // memo
  // -----------
  const TabsMemoComponent = useMemo(
    () => (
      <StyledTabs
        value={value}
        onChange={handleChange}
        aria-label="basic tabs"
        textColor="inherit"
        className="StyledTabs"
        variant="scrollable"
        scrollButtons="auto"
      >
        <bm.Tab label={bm.reference.ui.chart.name[lang]} {...allyProps(0)} />
        <bm.Tab label={bm.reference.ui.bars.name[lang]} {...allyProps(1)} />
        <bm.Tab label={bm.reference.ui.table.name[lang]} {...allyProps(2)} />
        <bm.Tab
          label={bm.reference.ui.description.name[lang]}
          {...allyProps(3)}
        />
        <bm.Tab label={bm.reference.ui.source.name[lang]} {...allyProps(4)} />
      </StyledTabs>
    ),
    [value, lang]
  );

  // -----------
  // effects
  // -----------

  // -----------
  // rendering
  // -----------
  return (
    <StyledTabsWrap className="RBasicTabs" sx={_styleRBasicTabs()}>
      <bm.Box
        sx={{
          borderBottom: 1,
          borderColor: (theme) =>
            (
              theme.palette.secondary as typeof theme.palette.secondary &
                Record<"other", string>
            ).other["400"] + "A1",
        }}
        className="RBasicTabs_Box"
      >
        {TabsMemoComponent}
      </bm.Box>
      {/* Line */}
      <TabPanel value={value} index={0} sx={{ height: BASE_HEIGHT }}>
        <bm.RLine
          data={data}
          axisTitles={axisTitles}
          lang={lang}
          minMaxY={minMaxY}
          target={target}
          xArray={xArray}
          xRange={xRange}
          handleChangedXRange={handleChangedXRange}
          rangeTupple={rangeTupple}
          baseHeight={BASE_HEIGHT}
          setCurrentTab={props.fnSetCurrentTabName}
        />
      </TabPanel>
      {/* Bar */}
      <TabPanel value={value} index={1} sx={{ height: BASE_HEIGHT }}>
        <bm.RMainResponsiveBar
          xRange={xRange}
          xArray={xArray}
          data={data}
          target={target}
          lang={lang}
          handleChangedXRange={handleChangedXRange}
          rangeTupple={rangeTupple}
          dataID={dataID}
          baseHeight={BASE_HEIGHT}
          setCurrentTab={props.fnSetCurrentTabName}
        />
      </TabPanel>
      {/* Table */}
      <TabPanel value={value} index={2} sx={{ height: BASE_HEIGHT }}>
        <bm.RBaseTable
          data={data}
          axisTitles={axisTitles}
          lang={lang}
          xArray={xArray}
          target={target}
          xRange={xRange}
          handleChangedXRange={handleChangedXRange}
          rangeTupple={rangeTupple}
          baseHeight={BASE_HEIGHT}
          setCurrentTab={props.fnSetCurrentTabName}
        />
      </TabPanel>
      {/* Description */}
      <TabPanel value={value} index={3} sx={{ height: BASE_HEIGHT }}>
        <RDescription
          lang={lang}
          target={target}
          setCurrentTab={props.fnSetCurrentTabName}
          baseHeight={BASE_HEIGHT}
        />
      </TabPanel>
      {/* Source */}
      <TabPanel value={value} index={4} sx={{ height: BASE_HEIGHT }}>
        Item Five
      </TabPanel>
    </StyledTabsWrap>
  );
}

// ===========
// subcomponents
// ===========
type TTabPanelProps = {
  children?: bm.React.ReactNode;
  index: number;
  value: number;
  sx?: SxProps<Theme>;
};

function TabPanel(props: TTabPanelProps) {
  const { children, value, index } = props;
  const style = { ...props.sx };

  // -----------
  // rendering
  // -----------
  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      className="TabPanel"
      style={style}
    >
      {value === index && (
        <bm.Box
          sx={{
            // p: 3,
            paddingTop: 0,
            // backgroundColor: "yellowgreen",
            paddingLeft: 0,
            height: "100%",
          }}
        >
          {children}
        </bm.Box>
      )}
    </div>
  );
}

// ===========
// allies
// ===========
function allyProps(index: number) {
  return {
    id: `simple-tab-${index}`,
    "aria-controls": `simple-tabpanel-${index}`,
  };
}

// ===========
// styles
// ===========
function _styleRBasicTabs() {
  return {
    width: "100%",
    "& .TabPanel": {
      // height: "100%",
    },
    "& .TabPanel > .MuiBox-root": {
      height: "100%",
      paddingBottom: 0,
      paddingLeft: "0px",
      paddingRight: "24px",
    },
    display: "flex",
    flexDirection: "column",
    justifyContent: "flex-start",
  };
}
