// ============
// import
// ============
import * as bm from "../barrelModule";

// ============
// thunks
// ============
export const fetchGeneric = (target: bm.TEWhatVal) =>
  bm.createAsyncThunk(
    `${target}/fetchGeneric_${target}`,

    async ({ target, cntrs }: bm.TFetchParams) => {
      const serverTarget = target.slice(target.indexOf("_") + 1);
      const data = await Promise.all(
        cntrs.map((country) => bm.fetchData(serverTarget, country))
      );
      return { countries: cntrs, data, target };
    }
  );
