// import
// =============
import * as React from "react";
import Box from "@mui/material/Box";
/* app */
import { useMainContext } from "../../context/MainContextProvider";
import { themeSettings } from "../../theming/themeSettings";
import { RTopbarSearch } from "./RTopbarSearch";
import { RTopbarIcons } from "./PTopbarIcons";
import { useMediaQuery } from "@mui/material";
import { RTopbarSidebar } from "./RTopbarSidebar";
import { RTopbarTargetSidebar } from "./RTopbarTargetSidebar";

// ===========
// types
// ===========
type TComponentProps = {
  handleDrawerOpen: () => void;
  handleDrawerClose: () => void;
};

// =============
// constants
// =============
const SECONDS_TO_EXPIRE = 10000; // 10s

// =============
// main
// =============
export function RTopbarMain(props: TComponentProps) {
  // --------
  // local state
  // --------
  const [_isExpired, setIsExpired] = React.useState({});

  const localState = React.useRef({
    isExpired: false,
  });

  // --------
  // variables
  // --------
  const { theme, lang, changeLang, toggleColorMode } = useMainContext();
  const colors = themeSettings(theme.palette.mode);
  const isMobile = !useMediaQuery(theme.breakpoints.up("sm"));

  React.useEffect(() => {
    const ls = localState.current;
    return () => {
      // localState.current.isExpired = false;
      ls.isExpired = false;
    };
  });

  // ---------
  // rendering
  // ---------
  return (
    <Box
      className="RTopbarMain"
      sx={{
        display: "flex",
        justifyContent: "space-between",
        color: isMobile
          ? localState.current.isExpired
            ? theme.palette.secondary.main
            : (() => {
                window.setTimeout(() => {
                  localState.current.isExpired = true;
                  setIsExpired({});
                }, SECONDS_TO_EXPIRE);
                return theme.palette.secondary.light;
              })()
          : theme.palette.secondary.light,
        transition: "color 2s",
      }}
    >
      {isMobile && (
        <RTopbarSidebar
          handleDrawerOpen={props.handleDrawerOpen}
          handleDrawerClose={props.handleDrawerClose}
        />
      )}
      <RTopbarSearch colors={colors} lang={lang} isMobile={isMobile} />
      <RTopbarIcons
        colors={colors}
        lang={lang}
        changeLang={changeLang}
        toggleColorMode={toggleColorMode}
        isMobile={isMobile}
      />
      {isMobile && <RTopbarTargetSidebar />}
    </Box>
  );
}
