// ===========
// import
// ===========
import * as bm from "../barrelModule";
import { RStyledDescription } from "../../../../styled/RStyledDescription";
import { ETabsNames } from "../tabs/RBasicTabs";

// ===========
// types
// ===========
type TComponentProps = {
  lang: bm.ELangs;
  target: bm.TEWhatVal;
  setCurrentTab: (tab: ETabsNames) => void;
  baseHeight: string;
};
type TResult = {
  baseHeight: string;
  EN:
    | {
        id: string;
        name: string;
        description: string;
        sourceOrganization: string;
        topics: string;
      }
    | undefined;
  RU:
    | {
        id: string;
        name: string;
        description: string;
        sourceOrganization: string;
        topics: string;
      }
    | undefined;
};

// ===========
// constants
// ===========
const URL_TEMPLATE = "/data/description/:target/wb.json";

