// =============
// import
// =============
import * as bm from "./barrelModule";
import React from "react";
import { Box } from "@mui/material";
import { useMainContext } from "../barrelModule";
// import { RRangeSlider } from "../sliders/RRangeSlider";
import { type TEWhatVal } from "./barrelModule";
import { ETabsNames } from "../tabs/RBasicTabs";

// =============
// types
// =============
type TComponentProps = {
  data: Array<bm.TRline>;
  axisTitles: { x: string; y: string };
  lang: bm.ELangs;
  xArray: Array<string> | null;
  target: TEWhatVal;
  xRange: Array<string> | null;
  handleChangedXRange: (range: [number, number]) => void;
  rangeTupple: [] | [number, number];
  baseHeight: string;
  setCurrentTab: (tab: ETabsNames) => void;
};
type TRows = Array<{ [key: string]: string | number } & { x: string }> | null;

// =============
// constants
// =============
const WIDTH_KEF = 10;
const PADDING_KEF = 2;
const ROW_HEAD_KEF = 6;
const COLUMN_WIDTH = `${WIDTH_KEF}em`;
const ROW_HEAD_WIDTH = `${ROW_HEAD_KEF}em`;

// =============
// main
// =============
export function RBaseTable({
  data,
  axisTitles,
  lang,
  xArray,
  target,
  // handleChangedXRange,
  // rangeTupple,
  baseHeight,
  setCurrentTab,
}: TComponentProps) {
  // -------------
  // from context
  // -------------
  const { isAnimation, saveTargetInContext } = useMainContext();

  // -------------
  // local state
  // -------------
  const [order, setOrder] = bm.useState<bm.TOrder>("asc");
  const [orderBy, setOrderBy] = bm.useState<string>("");

  // -------------
  // id of the "data"
  // -------------
  const dataID = bm.useMemo(
    () =>
      data
        .map((elm) => elm.id)
        .sort()
        .join(""),
    [data]
  );

  // -------------
  // handlers
  // -------------
  const handleRequestSort = bm.useCallback(
    (
      _event: React.MouseEvent<unknown>,
      property: string | bm.TECountriesVal
    ) => {
      const newOrderBy = orderBy === "" ? property : orderBy;
      const isAsc = newOrderBy === property && order === "asc";
      setOrder(isAsc ? "desc" : "asc");
      setOrderBy(property);
    },
    [order, orderBy]
  );

  // -------------
  // local store
  // -------------
  const localStore = bm.useRef({
    rows: null as TRows,
    oldDataID: "",
    oldTarget: "",
  });
  const ls = localStore.current;

  // -------------
  // old data check
  // -------------
  ls.rows = _rowMaker(xArray, data);
  ls.oldDataID = dataID;
  ls.oldTarget = target;
  // }

  // -------------
  // names array
  // -------------
  const names = data.map((elm) => ({ id: elm.id }));

  // ------------
  // Header Title to UpperCase
  // ------------
  axisTitles = Object.assign({}, axisTitles, {
    x: axisTitles.x.charAt(0).toUpperCase() + axisTitles.x.slice(1),
  });

  const TableContainerWKS = bm.WebKitScrollbar(bm.TableContainer);

  // -------------
  // effects
  // -------------
  React.useEffect(() => {
    saveTargetInContext(target);
  }, [target, saveTargetInContext]);

  React.useEffect(() => {
    setCurrentTab(ETabsNames.TABLE);
  }, []);

  // -------------
  // rendering
  // -------------
  if (!isAnimation && ls.rows !== null && ls.rows.length > 0) {
    return (
      <Box
        sx={{
          display: "flex",
          flexDirection: "column",
          // alignItems: "center",
          alignItems: "flex-start",
          justifyContent: "flex-between",
          height: "100%",
        }}
        className="RBaseTable_Wrapper"
      >
        <TableContainerWKS
          className="TableContainer"
          sx={{
            // height: baseHeight,
            height: "100%",
            maxWidth: `${
              names.length * WIDTH_KEF + ROW_HEAD_KEF + PADDING_KEF
            }rem`,
          }}
        >
          <bm.Table stickyHeader aria-label="simple table">
            <bm.REnhancedTableHead
              order={order}
              orderBy={orderBy}
              onRequestSort={handleRequestSort}
              xArrayTitle={{ id: "x" }}
              titleIDs={names}
              axisTitles={axisTitles}
              lang={lang}
              cellWidth={COLUMN_WIDTH}
              rowHeadWidth={ROW_HEAD_WIDTH}
            />
            <bm.TableBody>
              {ls.rows
                .slice()
                .sort(bm.getComparator(order, orderBy))
                .map((row) => {
                  return (
                    <bm.TableRow hover key={row.x}>
                      <bm.TableCell
                        component="th"
                        align="left"
                        scope="row"
                        sx={{ width: 0 }}
                      >
                        {row.x}
                      </bm.TableCell>
                      {names.map((obj) => (
                        <bm.TableCell
                          key={obj.id}
                          sx={{ width: ROW_HEAD_WIDTH }}
                          align="right"
                        >
                          {row[obj.id]}
                        </bm.TableCell>
                      ))}
                    </bm.TableRow>
                  );
                })}
            </bm.TableBody>
          </bm.Table>
        </TableContainerWKS>
      </Box>
    );
  } else {
    return (
      <Box
        sx={{
          /*marginBottom: "1em"*/
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
          justifyContent: "flex-between",
        }}
        className="RBaseTable_Wrapper"
        style={{ visibility: "hidden" }}
      >
        <TableContainerWKS
          className="TableContainer"
          sx={{
            height: baseHeight,
            maxWidth: `${
              names.length * WIDTH_KEF + ROW_HEAD_KEF + PADDING_KEF
            }rem`,
          }}
        >
          <div></div>
        </TableContainerWKS>
      </Box>
    );
  }
}

// -------------
// _rowMaker
// -------------
function _rowMaker(xArray: Array<string> | null, arr: Array<bm.TRline>) {
  if (xArray === null) {
    return null;
  }

  const xArrayReverse = [...xArray].reverse();

  const resultArray = [];

  for (const xStr of xArrayReverse) {
    const res = {} as { [key: string]: string | number } & { x: string };
    res.x = xStr;

    const result = arr.reduce((res, obj) => {
      const detected = obj.data.find((elm) => elm.x === xStr);
      if (detected === undefined) {
        return res;
      } else {
        return {
          ...res,
          [obj.id]: detected.y,
        };
      }
    }, res);

    if (Object.keys(result).length > 1) {
      resultArray.push(result);
    }
  }
  return resultArray;
}
