// =============
// import
// =============
import * as React from "react";
import { createTheme, type ThemeOptions } from "@mui/material/styles";
/* app */
import { themeSettings } from "../theming/themeSettings";
import { ELangs } from "../enums/ELangs";
import { TECountriesVal, ECountries } from "../enums/ECountries";
import { CUtilIter } from "../classes/CUtilIter";
import { chartWrapperWorker } from "../workers/chartWrapperWorker";
import { prepareAxisTitlesWorker } from "../func/prepare/prepareAxisTitles";
import { getActiveCountries } from "../func/prepare/getActiveCountries";
import { serializedDataIds } from "../func/prepare/serelizedDataIds";
import { getLineMinMaxWorker } from "../func/prepare/getLineMinMax";
import { prepareXArrayWorker } from "../func/prepare/prepareXArray";
import { type Theme } from "@mui/material/styles";
import { type TEWhatVal } from "../enums/EWhat";

// =============
// types
// =============
type TReactChildNodes = {
  children: React.ReactNode;
};
export type TActiveCountry = {
  checked: boolean;
  name: TECountriesVal;
};
type FtogCntr = (countriesArray: Array<TActiveCountry>) => void;

type TContextValues = {
  theme: Theme;
  toggleColorMode: () => void;
  changeLang: (lang: ELangs) => void;
  lang: ELangs;
  updateScreenSize: (width: number, height: number) => void;
  screenSize: { width: number; height: number };
  activeCoutries: Array<TActiveCountry>;
  toggleActiveCountries: (x: Array<TActiveCountry>) => void;
  chartWorker: Worker;
  isAnimation: boolean;
  changeIsAnimation: (isAnimationNew: boolean) => void;
  targetInContext: TEWhatVal;
  saveTargetInContext: (target: TEWhatVal) => void;
  isTargetSidebarOpen: boolean;
  saveStateOfTargetSidebarInContext: (isOpen: boolean) => void;
};

// =============
// const
// =============

// =============
// MainContext
// =============
const MainContext = React.createContext<TContextValues>({} as TContextValues);

// =============
// useMainContext
// =============
export const useMainContext = () => React.useContext(MainContext);

// =============
// MainContextProvider
// =============
export function MainContextProvider({ children }: TReactChildNodes) {
  const [colorMode, setColorMode] = React.useState<"dark" | "light">("dark");
  const [lang, setLang] = React.useState<ELangs>(ELangs.EN);
  const [screenSize, setScreenSize] = React.useState(_startScreenSize());
  const [activeCoutries, setActiveCountries] = React.useState(
    _activeCountries()
  );
  const [isAnimation, setIsAnimation] = React.useState(false);
  const [targetInContext, setTargetInContext] =
    React.useState<TEWhatVal>("output_gdp");
  const [isTargetSidebarOpen, setIsTargetSidebarOpen] = React.useState(false);

  // const getIsAnimation = () => isAnimation;

  const toggleColorMode = _toggleColorMode(setColorMode);
  const theme = _toggleTheme(colorMode);
  const changeLang = _toggleLang(setLang);
  const updateScreenSize = _updateScreenSize(setScreenSize);
  const toggleActiveCountries: FtogCntr =
    _toggleActiveCountries(setActiveCountries);
  const chartWorker: Worker = React.useMemo(() => {
    const code = chartWrapperWorker.toString();
    const blob = new Blob([
      `(${code})({
        prepareAxisTitles: ${prepareAxisTitlesWorker}, 
        getActiveCountries: ${getActiveCountries},
        serializedDataIds: ${serializedDataIds},
        getLineMinMax: ${getLineMinMaxWorker},
        prepareXArray: ${prepareXArrayWorker},
      })`,
    ]);
    return new Worker(URL.createObjectURL(blob));
  }, []);
  const changeIsAnimation = _changeIsAnimation(setIsAnimation);
  const saveTargetInContext = (target: TEWhatVal) => setTargetInContext(target);
  const saveStateOfTargetSidebarInContext = (isOpen: boolean) =>
    setIsTargetSidebarOpen(isOpen);

  // ---------
  // export
  // ---------
  return (
    <MainContext.Provider
      value={{
        theme,
        toggleColorMode,
        lang,
        changeLang,
        updateScreenSize,
        screenSize,
        activeCoutries,
        toggleActiveCountries,
        chartWorker,
        isAnimation,
        changeIsAnimation,
        saveTargetInContext,
        targetInContext,
        isTargetSidebarOpen,
        saveStateOfTargetSidebarInContext,
      }}
    >
      {children}
    </MainContext.Provider>
  );
}

// ==============
// utilities
// ==============
function _toggleColorMode(
  setColorMode: React.Dispatch<React.SetStateAction<"dark" | "light">>
) {
  return React.useMemo(
    () => () => setColorMode((prev) => (prev === "light" ? "dark" : "light")),
    // eslint-disable-next-line
    []
  );
}

// -------------
function _toggleTheme(colorMode: "dark" | "light") {
  const result = React.useMemo(() => {
    const themeOptions: ThemeOptions = themeSettings(colorMode);

    const newTheme = Object.assign({}, themeOptions);

    const createdTheme = createTheme(newTheme);

    return createdTheme;
  }, [colorMode]);
  return result;
}

// -------------
// _toggleLang
// -------------
function _toggleLang(setLang: any) {
  return React.useMemo(
    () => (lang: ELangs) => setLang(lang),
    // eslint-disable-next-line
    []
  );
}

// -------------
function _startScreenSize() {
  return {
    width: window.innerWidth,
    height: window.innerHeight,
  };
}

// -------------
function _updateScreenSize(
  setScreenSize: React.Dispatch<
    React.SetStateAction<{ width: number; height: number }>
  >
) {
  return (width: number, height: number) => setScreenSize({ width, height });
}

// -------------
function _activeCountries(): Array<TActiveCountry> {
  const result = [] as Array<TActiveCountry>;
  const countries = new CUtilIter(ECountries);

  for (const country of countries) {
    if (country === undefined) {
      throw new Error(
        "MainContextProvider::_activeCountries::[ERROR]: 'country' === undefined"
      );
    }

    const activeCountry = {
      checked: true,
      name: country[1],
    } as TActiveCountry;

    result.push(activeCountry);
  }

  return result;
}

// -------------
function _toggleActiveCountries(
  setActiveCountries: React.Dispatch<
    React.SetStateAction<Array<TActiveCountry>>
  >
) {
  const fn = React.useMemo(
    () => (countriesArray: Array<TActiveCountry>) => {
      setActiveCountries((oldArray) => {
        const newArray = [] as Array<TActiveCountry>;
        oldArray.forEach((obj) => {
          const updatedIndex = countriesArray.findIndex(
            (country) => country.name === obj.name
          );
          if (updatedIndex === -1) {
            newArray.push(obj);
          } else {
            const updatedCountries = {
              checked: countriesArray[updatedIndex].checked,
              name: obj.name,
            } as TActiveCountry;
            newArray.push(updatedCountries);
          }
        });
        return newArray;
      });
    },
    // eslint-disable-next-line
    []
  );

  return fn;
}

// -------------
function _changeIsAnimation(
  setIsAnimation: React.Dispatch<React.SetStateAction<boolean>>
  // getIsAnimation: () => boolean
) {
  return (isAnimationNew: boolean) => {
    // if (getIsAnimation() !== isAnimationNew) {
    setIsAnimation(isAnimationNew);
    // } else {
    /* nothing */
    // }
  };
}
