// ============
// import
// ============
import * as bm from "../barrelModule";
import { countriesAdapter } from "./adapter";

// ============
// slice
// ============
const countriesSlice = bm.createSlice({
  name: "countries",
  initialState: countriesAdapter.getInitialState({
    status: bm.EStatus.IDLE,
  }),
  reducers: {
    addManyCountries(state, action) {
      state.status = bm.EStatus.SUCCEEDED;
      countriesAdapter.addMany(state, action.payload);
    },
    toggleCountryActive(state, action) {
      state.status = bm.EStatus.IDLE;
      countriesAdapter.updateOne(state, action.payload);
    },
    toggleAllCountries(state, action) {
      state.status = bm.EStatus.IDLE;
      countriesAdapter.updateMany(state, action.payload);
    },
    updateCountryStatusSU(state) {
      state.status = bm.EStatus.SUCCEEDED;
    },
  },
});

// ============
// reducer
// ============
export default countriesSlice.reducer;

// ============
// actions
// ============
export const {
  addManyCountries,
  toggleCountryActive,
  toggleAllCountries,
  updateCountryStatusSU,
} = countriesSlice.actions;
