// ===========
// import
// ===========
import * as bm from "../barrelModule";
import { useMemo } from "react";
import { RStyledHorizontalTabsWrap as StyledHomeTabs } from "../../../../styled/RStyledHorizontalTabsWrap";
import { RStyledHorizontalTabs as StyledTabs } from "../../../../styled/RStyledHorizontalTabs";
import { useMainContext } from "../barrelModule";
import { useMediaQuery } from "@mui/material";

// ===========
// types
// ===========
type TComponentProps = {
  lang: bm.ELangs;
};

// ===========
// styled
// ===========

// ===========
// main
// ===========
export function RHomeTabs(props: TComponentProps) {
  const [value, setValue] = bm.useState(0);

  const { lang } = props;
  const { theme } = useMainContext();
  const isMobile = !useMediaQuery(theme.breakpoints.up("sm"));

  // -----------
  // handlers
  // -----------
  const handleChange = (_event: bm.React.SyntheticEvent, newValue: number) => {
    setValue(newValue);
  };

  // -----------
  // memo
  // -----------
  const TabsMemoComponent = useMemo(
    () => (
      <StyledTabs
        value={value}
        onChange={handleChange}
        aria-label="basic tabs"
        textColor="inherit"
      >
        <bm.Tab
          value={0}
          label={bm.reference.ui.selectCountries.name[lang]}
          {...allyProps(0)}
          className="RHomeTabs_StyledTabs_Tab_0"
        />
        <bm.Tab
          value={1}
          label={bm.reference.ui.details.name[lang]}
          {...allyProps(1)}
          className="RHomeTabs_StyledTabs_Tab_1"
        />
        <bm.Tab
          value={2}
          label={bm.reference.ui.about.name[lang]}
          {...allyProps(2)}
          className="RHomeTabs_StyledTabs_Tab_2"
        />
      </StyledTabs>
    ),
    [value, lang]
  );

  // -----------
  // rendering
  // -----------
  return (
    <StyledHomeTabs className="RHomeTabs">
      <bm.Box
        className="RHomeTabs_before_TabsMemoComponent"
        sx={{ borderBottom: 1, borderColor: "divider" }}
      >
        {TabsMemoComponent}
      </bm.Box>
      <TabPanel value={value} index={0} isMobile={isMobile}>
        <bm.RStartTab lang={lang} />
      </TabPanel>
      <TabPanel value={value} index={1}>
        <bm.RStartTab lang={lang} />
      </TabPanel>
      <TabPanel value={value} index={2}>
        <bm.RStartTab lang={lang} />
      </TabPanel>
    </StyledHomeTabs>
  );
}

// ===========
// subcomponents
// ===========
type TTabPanelProps = {
  children?: bm.React.ReactNode;
  index: number;
  value: number;
  isMobile?: boolean;
};

function TabPanel(props: TTabPanelProps) {
  const { children, value, index, ...other } = props;

  // -----------
  // rendering
  // -----------
  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      className="TabPanel"
    >
      {value === index && (
        <bm.Box
          sx={{
            p: 3,
            paddingTop: 0,
            paddingLeft: other.isMobile ? 0 : 3,
          }}
          className="RHomeTabs_TabPanel_Box"
        >
          {children}
        </bm.Box>
      )}
    </div>
  );
}

// ===========
// allies
// ===========
function allyProps(index: number) {
  return {
    id: `simple-tab-${index}`,
    "aria-controls": `simple-tabpanel-${index}`,
  };
}
