// =============
// import
// =============
import reference from "../../reference.json";
import type { TECountriesVal } from "../../enums/ECountries";

// =============
// types
// =============
type TData = {
  id: TECountriesVal;
  data: Array<{ x: string; y: number }>;
};

// =============
// main
// =============
export function pickCountryColor(data: Array<TData>) {
  return data.map((obj) => {
    const cnt = obj.id;
    const colorCountry = reference.countries[cnt].colors.main;
    return colorCountry;
  });
}
