// styles
// ===========
function _stylesListItemIcon() {
  return {
    minWidth: 0,
    // mr: open ? 3 : "auto",
    // ml: open ? "auto" : 3,
    ml: "auto",
    justifyContent: "center",
    "& .MuiSvgIcon-root": {
      width: "1.5rem",
      height: "1.5rem",
    },
  };
}

function _stylesListItemText(open: boolean) {
  return {
    opacity: open ? 1 : 0,
    whiteSpace: "nowrap",
    transition: "opacity 0.01s 2s",
  };
}

function _stylesBox() {
  return {
    display: "flex",
  };
}
