// ============
// import
// ============
import * as bm from "../barrelModule";

// ============
// adapter
// ============
export const genericAdapter = bm.createEntityAdapter<bm.TRline>({
  sortComparer: (a, b) => a.minX.localeCompare(b.minX),
});

// ============
// selectors
// ============
export function genericSelectors(target: bm.TEWhatVal) {
  return genericAdapter.getSelectors((state: bm.RootState) => state[target]);
}

// ------------
// custom selectors
// ------------
export const selectMinMaxYGeneric =
  (target: bm.TEWhatVal) => (state: bm.RootState) => ({
    minY: state[target].minY,
    maxY: state[target].maxY,
  });

export const selectMinMaxXGeneric =
  (target: bm.TEWhatVal) => (state: bm.RootState) => ({
    minX: state[target].minX,
    maxX: state[target].maxX,
    stepX: state[target].stepX,
  });
