// =============
// import
// =============
import * as bm from "./barrelModule";
import { RTargetsNavTabs } from "../targetsbar/RTargetsNavTabs";
import reference from "../../../reference.json";
import * as React from "react";
import { type TCountries } from "./line/legend/barrelModule";
import { ETabsNames } from "./tabs/RBasicTabs";
import { RRangeSlider } from "./sliders/RRangeSlider";

// =============
// types
// =============
type TComponentProps = {
  target: bm.TEWhatVal;
  lang: bm.ELangs;
};
export type TThemeColors = Record<"colors" | "theme", Record<string, any>>;

// =============
// main
// =============
export function RMainChartWrapper(props: TComponentProps) {
  // ----------
  // local state
  // ----------
  const [isChangedFlagObj, setIsChangedFlag] = React.useState({ flag: true });
  const [rangeTupple, setRangeTupple] = React.useState<[number, number]>(
    [] as unknown as [number, number]
  );
  const [isRangeTuppleChanged, setIsRangeTuppleChanged] = React.useState(false);
  const [currentTabName, setCurrentTabName] = React.useState<ETabsNames>(
    ETabsNames.CHART
  );
  const [toShowLegend, setToShowLegend] = React.useState(true);

  const lang = bm.useMemo(() => props.lang, [props.lang]);
  const target = bm.useMemo(() => props.target, [props.target]);

  // blockStart------------------
  const { theme, chartWorker } = bm.useMainContext();
  const themeColors: TThemeColors = bm.useMemo(
    () => bm.getThemeColors(theme, bm.themeSettings),
    []
  );
  const themeAlias = theme as Record<string, any>;

  const matches = bm.useMediaQuery(themeAlias.breakpoints.up("sm"));
  const marginRight = matches
    ? Number(bm.config.layout.charts.line.margin.right.md)
    : Number(bm.config.layout.charts.line.margin.right.xs);
  // blockEnd--------------------

  const {
    selectorData,
    selectorStatus,
    fetchFromServer,
    countriesRedux: countriesDic,
    countriesReduxIds: countriesIds,
  } = bm.usePreMainContent(target);

  let dataForWorkerObj = {};

  const dispatch = bm.useAppDispatch();
  const dataRedux = bm.useAppSelector(selectorData);

  const reduxStatus = bm.useAppSelector(selectorStatus);

  // -----------
  // local store
  // -----------
  const localStore = bm.useRef({
    isMinMaxChanged: false,
    isCountryStatusChanged: false,
    lineMM: { minY: null, maxY: null, minX: null, maxX: null, stepX: null } as {
      minY: number | null;
      maxY: number | null;
      minX: number | null;
      maxX: number | null;
      stepX: number | null;
    },
    oldData: "",
    oldTarget: "",
    workerStatus: "idle" as "idle" | "running" | "succeeded" | "failed",
    data: null as null | Array<{
      id: string;
      maxX: string;
      maxY: number;
      minX: string;
      minY: number;
      stepX: number;
      stepY: number;
    }>,
    xArray: null as Array<string> | null,
    xRange: null as Array<string> | null,
    axisTitles: null as null | { x: string; y: string },
    transactionInAction: false as boolean,
    // currentTabName: ETabsNames.CHART as ETabsNames,
    oldLang: "" as bm.ELangs,
  });

  const newData = Object.values(countriesDic)
    .reduce((result, element) => {
      if ((element as TCountries).active === true) {
        result.push((element as TCountries).id);
      }
      return result;
    }, [] as Array<string>)
    .join("");

  // data changing status
  if (
    newData !== localStore.current.oldData ||
    target !== localStore.current.oldTarget ||
    isRangeTuppleChanged
  ) {
    localStore.current.transactionInAction = true;
  }

  // ----------
  // utility fn
  // ----------
  const fnSetCurrentTabName = (tabName: ETabsNames) => {
    setCurrentTabName(tabName);
  };
  // const fnGetCurrentTabName = () => currentTabName;

  // ----------
  // to worker
  // ----------
  if (
    window.Worker &&
    reduxStatus === "succeeded" &&
    localStore.current.transactionInAction === true &&
    localStore.current.workerStatus === "idle"
  ) {
    dataForWorkerObj = {
      message: "test",
      payload: {
        target,
        lang,
        reference,
        dataRedux,
        countriesDic,
        oldData: localStore.current.oldData,
        oldTarget: localStore.current.oldTarget,
        rangeXMin: rangeTupple[0] ? rangeTupple[0] : null,
        rangeXMax: rangeTupple[1] ? rangeTupple[1] : null,
        isRangeTuppleChanged,
        xRange: localStore.current.xRange,
        xArray: localStore.current.xArray,
      },
    };
    localStore.current.workerStatus = "running";
    chartWorker.postMessage(dataForWorkerObj);
  } else {
    // --------------
    // if lang changed (worker === 'idle')
    // --------------
    if (localStore.current.oldLang !== lang) {
      localStore.current.axisTitles = {
        x: reference.targets[target].x[lang],
        y: reference.targets[target].y[lang],
      };
    }
  }

  // ----------
  // from worker
  // ----------
  if (window.Worker) {
    chartWorker.onmessage = <
      T extends { message: string; payload: Record<string, any> }
    >(
      evnt: MessageEvent<T>
    ) => {
      const result = evnt.data;

      switch (result.message) {
        case "succeeded": {
          if (process.env.NODE_ENV === "development") {
            // Some 'bag' in Chrome browser
            if (localStore.current.workerStatus === "succeeded") break;
          }

          if (
            result.payload.isDataChanged ||
            result.payload.isTargetChanged ||
            result.payload.isRangeXChanged
          ) {
            (<
              T extends Partial<typeof localStore.current>,
              U extends T & Record<"minMaxY" | "minMaxX", any>,
              K extends keyof T
            >(
              mainObject: T,
              payload: U
            ) => {
              (
                [
                  "oldData",
                  "oldTarget",
                  "data",
                  "xArray",
                  "xRange",
                  "axisTitles",
                ] as Array<K>
              ).forEach((item) => (mainObject[item] = payload[item]));

              let lineMM = {} as typeof mainObject.lineMM;

              ["min", "max"].forEach(
                (item) =>
                  (lineMM = Object.assign({}, lineMM, {
                    [`${item}Y`]: payload.minMaxY[item],
                  }))
              );
              ["min", "max", "step"].forEach(
                (item) =>
                  (lineMM = Object.assign({}, lineMM, {
                    [`${item}X`]: payload.minMaxX[item],
                  }))
              );

              mainObject.lineMM = lineMM;

              localStore.current = Object.assign(
                {},
                localStore.current,
                mainObject
              );
            })({}, result.payload);

            if (result.payload.isRangeXChanged) {
              setIsRangeTuppleChanged(false);
            }

            localStore.current.transactionInAction = false;
          }

          setIsChangedFlag(() =>
            Object.assign({}, { flag: !isChangedFlagObj.flag })
          );

          if (localStore.current.transactionInAction === false) {
            localStore.current.workerStatus = "idle";
          }

          break;
        }

        default:
          break;
      }
    };
  }

  const minMaxY = {
    minY: localStore.current.lineMM.minY,
    maxY: localStore.current.lineMM.maxY,
  };

  // --------------
  // handlers
  // --------------
  const handleChangedXRange = (rangeX: [number, number]) => {
    setRangeTupple([rangeX[0], rangeX[1]]);
    setIsRangeTuppleChanged(true);
  };

  // -------------
  // effects
  // -------------
  /* 1 */
  /* fetch data */
  bm.useEffect(() => {
    if (reduxStatus === bm.EStatus.IDLE) {
      const cntrs = countriesIds as Array<bm.TECountriesVal>;
      dispatch(fetchFromServer({ target, cntrs }));
      localStore.current.transactionInAction = true;
    }
  }, [reduxStatus, dispatch, countriesIds, target, fetchFromServer]);

  /* 2 */
  // bm.useEffect(() => {
  //   if (localStore.current.workerStatus === "succeeded") {
  //     localStore.current.workerStatus = "idle";
  //   }
  // });

  React.useEffect(() => {
    switch (currentTabName) {
      case ETabsNames.DESCRIPTION:
        setToShowLegend(false);
        break;

      case ETabsNames.SOURCE:
        setToShowLegend(false);
        break;

      default:
        setToShowLegend(true);
        break;
    }

    return () => {
      // setToShowLegend(true);
    };
  }, [currentTabName]);

  // -------------
  // Memo
  // -------------
  const isDark = bm.useMemo(
    () => (themeColors.colors.palette.mode === "dark" ? true : false),
    [themeColors.colors.palette.mode]
  );

  const TargetsNavBarMemoComponent = bm.useMemo(
    () => <RTargetsNavTabs target={target} themeColors={themeColors} />,
    [target, themeColors]
  );

  const LegendMemoComponent = bm.useMemo(
    () =>
      (
        // fnGetCurrentTabName: () => ETabsNames,
        xArray: Array<string> | null,
        axisTitles: { x: string; y: string } | null,
        toShowBlock: boolean
      ) => {
        const aT = axisTitles === null ? { x: "", y: "" } : axisTitles;
        return (
          <bm.Box
            sx={{
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
              width: "100%",
              visibility: toShowBlock ? "visible" : "hidden",
            }}
            className="LegendMemoComponentWrapper"
          >
            {/* == Slider block == */}
            <bm.Box
              sx={{
                display: "flex",
                flexDirection: "column",
                alignItems: "center",
                width: "100%",
              }}
              className="LegendMemoComponentWrapper_Slider"
            >
              {/* Axis Title (x) */}
              <span style={{ marginTop: "1em" }}>
                {aT.x.toLocaleLowerCase()}
              </span>

              {/* Slider */}
              <RRangeSlider
                xArray={xArray}
                handleChangedXRange={handleChangedXRange}
                rangeTupple={rangeTupple}
                className="RMainChartWrapper_RRangeSlider"
                sx={{ width: "92%", marginTop: "1em" }}
              />
            </bm.Box>

            {/* == Legend block == */}
            <bm.RLineLegend
              lang={lang}
              colors={themeColors.colors.palette}
              isDark={isDark}
              marginRight={marginRight}
              // fnGetCurrentTabName={fnGetCurrentTabName}
            />
          </bm.Box>
        );
      },
    [lang, themeColors.colors.palette, isDark, marginRight, currentTabName]
  );

  // -------------
  // before rendering
  // -------------
  localStore.current.oldLang = lang;

  // -------------
  // rendering
  // -------------
  return (
    <bm.Box className="RMainChartWrapper" sx={_wrapperStyle(matches)}>
      <bm.RBasicTabs
        data={localStore.current.data === null ? [] : localStore.current.data}
        axisTitles={
          localStore.current.axisTitles === null
            ? { x: "", y: "" }
            : localStore.current.axisTitles
        }
        lang={lang}
        minMaxY={minMaxY}
        xArray={localStore.current.xArray}
        xRange={localStore.current.xRange}
        target={target}
        handleChangedXRange={handleChangedXRange}
        rangeTupple={rangeTupple}
        dataID={localStore.current.oldData}
        fnSetCurrentTabName={fnSetCurrentTabName}
      />
      {matches && TargetsNavBarMemoComponent}
      {LegendMemoComponent(
        // fnGetCurrentTabName,
        localStore.current.xArray,
        localStore.current.axisTitles,
        toShowLegend
      )}
    </bm.Box>
  );
}

// =============
// styles
// =============
function _wrapperStyle(matches: boolean) {
  const DESKTOP_WIDTH = "85% 1fr";
  const MOBILE_WIDTH = "100%";
  return {
    display: "grid",
    gridTemplateColumns: `${matches ? DESKTOP_WIDTH : MOBILE_WIDTH}`,
    // gridTemplateRows: "0.98fr min-content",
    gridTemplateRows: "1fr min-content",
    minWidth: 0,
    // overflow: "hidden",
  };
}
