// ============
// import
// ============
import * as bm from "../barrelModule";

// ============
// adapter
// ============
export const countriesAdapter = bm.createEntityAdapter<bm.TCountries>({
  sortComparer: (a, b) => a.id.localeCompare(b.id),
});

// ============
// selectors
// ============
export const { selectAll, selectById, selectIds, selectEntities } =
  countriesAdapter.getSelectors((state: bm.RootState) => state.countries);

// ------------
// custom selectors
// ------------
export const selectStatusCountries = (state: bm.RootState) =>
  state.countries.status;
