// =============
// import
// =============
import type { TThemeSettings } from "../../theming/themeSettings";

// =============
// types
// =============
type TTheme = {
  palette: { [x: string]: any };
};

// =============
// main
// =============
export function getThemeColors(theme: TTheme, themeSettings: TThemeSettings) {
  return {
    theme,
    colors: themeSettings(theme.palette.mode),
  };
}
