// ==========
// import
// ==========
import Typography from "@mui/material/Typography";
import Stack from "@mui/material/Stack";
import Breadcrumbs from "@mui/material/Breadcrumbs";
/* app */
import type { TPageUtils } from "../../content/RMainContent";
import type { TColors } from "../../../types/main-types";
import { ELangs } from "../../../enums/ELangs";
import reference from "../../../reference.json";
import { useLocation } from "react-router-dom";

// ==========
// types
// ==========
type TTitle = string | (string & keyof typeof reference.greetings);
type TComponentProps = {
  title: TTitle;
  utils: TPageUtils;
  lang: ELangs;
};

// ==========
// regex
// ==========
const regex = /[^/_]+/i;

// ==========
// constants
// ==========
const _colorForHeader = (colors: Omit<TColors, "typography">) =>
  colors.palette.text.secondary;
const _colorForSubHeader = (colors: Omit<TColors, "typography">) =>
  colors.palette.text.primary;

// ==========
// main
// ==========
export function RHeader({ title, utils, lang }: TComponentProps) {
  const [mainTitle, subTitle] = _localTitles(title, utils.lang);

  // -------
  // URL params
  // -------
  const location = useLocation();
  // const urlParams = useParams();
  // const [urlSearchParams] = useSearchParams();

  const regexMatch = location.pathname.match(regex) as Array<
    "output" | "gdp" | "cpi" | "population"
  > | null;

  const section =
    regexMatch !== null
      ? reference.directory.sections[regexMatch[0]].name[lang]
      : "";

  // -----------
  // rendering
  // -----------
  return (
    <Stack sx={_stackStyles()} className="RHeader_Stack">
      <Typography variant="h2" sx={_mainTitleStyle(utils.colors)}>
        {mainTitle}
      </Typography>
      <Typography variant="h5" sx={_subTitleStyle(utils.colors)}>
        {subTitle}
      </Typography>
      <Breadcrumbs
        aria-label="breadcrumb"
        className="RHeader_Breadcrumbs"
        sx={_breadCrumbsStyle(utils.colors)}
      >
        <Typography>{section}</Typography>
        <Typography>{mainTitle}</Typography>
      </Breadcrumbs>
    </Stack>
  );
}

// ----------
// _localTitles
// ----------
function _localTitles(title: TTitle, lang: ELangs) {
  let what = {} as Record<string, any>;
  if (title in reference.targets) {
    what = reference.targets[title as keyof typeof reference.targets];
  } else if (title in reference.greetings) {
    what = reference.greetings[title as keyof typeof reference.greetings];
  } else {
    throw new Error(
      `RHeader::_localTitles::[ERROR]: "title" ${title} is not in "reference.targets" or "reference.greetings".`
    );
  }
  const mainTitle = ("shortName" as keyof typeof what)
    ? what.shortName[lang]
    : "Error";
  const subTitle = ("name" as keyof typeof what) ? what.name[lang] : "Error";

  return [mainTitle, subTitle];
}

// ==========
// styles
// ==========
// ----------
// _stackStyles
// ----------
function _stackStyles() {
  return {
    gap: "0.3rem",
  };
}
// ----------
// _mainTitleStyle
// ----------
function _mainTitleStyle({ ...colors }: TColors) {
  // const isDark = colors.palette.mode === "dark";

  return {
    color: _colorForHeader(colors),
    fontWeight: "bold",
  };
}

// ----------
// _subTitleStyle
// ----------
function _subTitleStyle({ ...colors }: TColors) {
  // const isDark = colors.palette.mode === "dark";

  return {
    marginLeft: "0.3rem",
    color: _colorForSubHeader(colors),
  };
}

// ----------
// _breadCrumbsStyle
// ----------
function _breadCrumbsStyle({ ...colors }: TColors) {
  return {
    paddingTop: "0.2rem",
    marginLeft: "0.4rem",
    fontStyle: "italic",
    "& .MuiTypography-root": {
      fontSize: "0.7rem",
    },
    "& .MuiBreadcrumbs-separator": {
      fontSize: "0.7rem",
    },
    color: _colorForSubHeader(colors),
  };
}
