const PALETTE_COLORS_KEYS = Object.keys(PALETTE_COLORS);

// ===========
// main
// ===========
export default function DevelopViews() {
  const { theme } = useMainContext();

  type TPalette = typeof theme.palette;

  const palette = theme.palette;
  let pl = [] as Array<{
    id: string;
    title: string;
    colors: Record<string, any>;
  }>;
  // Object.keys(palette).forEach((key: string) => {
  Object.keys(palette).forEach((key) => {
    if (PALETTE_COLORS_KEYS.includes(key)) {
      const k = key as keyof TPalette;

      pl.push({
        id: key,
        title: key,
        // colors: _toSortedArray(_arrayOfStyledViewsBlock(palette[key])),
        colors: _toSortedArray(
          _arrayOfStyledViewsBlock<TPalette[typeof k], never>(palette[k])
        ),
      });
    }
  });
  // console.log("%cdevelopViews/index/DevelopViews::pl: ", "color: orange", pl);

  // --------
  // rendering
  // --------
  return (
    <StyledViewsWrapper>
      <>
        {pl.map((block) => (
          <StyledViewsBlock key={block.id} data={block} />
        ))}
      </>
    </StyledViewsWrapper>
  );
}

// ===========
// utils
// ===========
function _arrayOfStyledViewsBlock<T, K extends keyof T>(
  colorObj: Pick<T, K>,
  parentKey?: string
) {
  let mainResult = {} as Record<string, any>;

  Object.keys(colorObj).forEach((key: string) => {
    const isPropObj = typeof colorObj[key as K] === "object" ? true : false;

    if (isPropObj) {
      const recurciveObject = colorObj[key as K];
      mainResult = Object.assign(
        {},
        mainResult,
        _arrayOfStyledViewsBlock<
          typeof recurciveObject,
          keyof typeof recurciveObject
        >(recurciveObject, key)
      );
    } else {
      mainResult[parentKey ? `${parentKey}_${key}` : `${key}`] =
        colorObj[key as K];
    }
  });

  return mainResult;
}

function _toSortedArray(objToSort: Record<string, any>) {
  const resultArray = [] as Array<Record<string, any>>;

  for (const [key, value] of Object.entries(objToSort)) {
    resultArray.push({ key, value });
  }

  resultArray.sort((a, b) => (a.key < b.key ? -1 : 1));
  return resultArray;
}
