// =============
// import
// =============
import * as React from "react";
import { CssBaseline, ThemeProvider } from "@mui/material";
import { Outlet } from "react-router-dom";
/* app */
import { useMainContext } from "./context/MainContextProvider";
import { RMiniDrawer } from "./components/sidebar_mui/RMiniDrawer";
import { RTopbarMain } from "./components/topbar/RTopbarMain";
import { useScreenSize } from "./hooks_g/useScreenResize";
import { useInitApp } from "./hooks_g/useInitApp";
import { useMediaQuery } from "@mui/material";
import { RTargetsDrawer } from "./components/topbar/targetsDrawer/RTargetsDrawer";

// =============
// main
// =============
function App() {
  // ----------
  // local state
  // ----------
  const [openMiniDrawer, setOpenMiniDrawer] = React.useState(false);

  // ---------
  // set up
  // ---------
  const { theme } = useMainContext();
  useScreenSize();
  useInitApp();
  const isMobile = !useMediaQuery(theme.breakpoints.up("sm"));

  // ---------
  // handlers
  // ---------
  const handleDrawerOpen = () => {
    setOpenMiniDrawer((old) => !old);
  };

  const handleDrawerClose = () => {
    setOpenMiniDrawer(false);
  };

  // ---------
  // rendering
  // ---------
  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <div className="app" style={{ display: "flex" }}>
        <RMiniDrawer
          open={openMiniDrawer}
          handleDrawerClose={handleDrawerClose}
          handleDrawerOpen={handleDrawerOpen}
        />
        {isMobile && <RTargetsDrawer />}
        <main className="App_main content" style={_mainStyles()}>
          <RTopbarMain
            handleDrawerOpen={handleDrawerOpen}
            handleDrawerClose={handleDrawerClose}
          />
          <Outlet />
        </main>
      </div>
    </ThemeProvider>
  );
}

export default App;

// =============
// styles
// =============
function _mainStyles() {
  return {
    padding: "1rem",
    display: "grid",
    gridTemplateRows: "max-content 1fr",
    rowGap: "2rem",
    overflow: "hidden",
  };
}
